import { ofType } from 'redux-observable';
import { mergeMap, map } from 'rxjs/operators';
import { from, Observable } from 'rxjs';

import { logAndCatchError } from '../utils/rxjs-utils';
import { resolveRestApi } from '../router/app-router';
import constants, { publicConstants } from '../constants';
import * as StateManager from '../store/state/state-manager';
import TargetMap from '../actions/targetmap';
import Utils from '../utils/utils';
import CaseUtils from '../case/utils';
import { getTranslatedDXAPIResponse } from '../utils/dxapi-response-translator';
import { getResolvedTargetName } from '../router/routing-manager';
import { ActionManagerInstance } from '../actions/manager';
import * as actionType from '../store/state/actions/index';
import * as actions from '../store/state/actions/action-creators';
import StateMachineManager from '../page-instructions/state-machine-manager';
import {
  processViewResponse,
  getActionPayload,
  processHeaders,
  processDataToDispatchErrorAction,
  getSubmitData,
  getParentContextChangeSet,
  mergeObjectsAndConcatArrays
} from '../store/state/epics/util';
import { fetch, getCancelTokenSource, getActiveRequestCountPerContext } from '../service_broker/fetch';
import * as RuleStore from '../rules/rules-store';
import type { Action } from '../store/state/state-manager';
import ActionsSequencer from '../actions/sequencer';
import type { ActionConfig, Payload } from '../contextree/types';
import EnvironmentInfo from '../environment-info';
import type { CalculatedField } from '../types';
import type { RestAPIPayload } from '../router/types';

import { appendQueryParamsForRefreshAPI, getItemsToRecalculate, shouldCancelRequest } from './refreshHelpers';
import type { ActionPayload, RefreshPayLoad, ReqConfig, RequestBody } from './types';

import refreshConstants from './index';

const reqConfigList: { [key: string]: any } = {};
const { VIEW, FIELD } = constants.CONTEXT_TREE_NODE_TYPES;

const prepareRequestContext = (payload: any) => {
  const { ID, actionID, context } = payload;
  return `${ID}.${context}.${actionID}`;
};

const prepareRequestHeaders = (reqConfig: ReqConfig) => {
  if (reqConfig.reqHeaders && reqConfig.reqHeaders.etag) {
    reqConfig.headers = { 'if-match': reqConfig.reqHeaders.etag };
  }
};

const hasActiveRequestsForContext = (reqContext: string) =>
  getActiveRequestCountPerContext(reqContext) > 0 && reqConfigList[reqContext];

/**
 * This helper function refresh the case view, case data or assignments
 * Track the pending requests fired for a view and cancel them if any subsequent
 * requests fired again for the same view.
 * @param reqConfig contains info about the http request
 * @param payload contains view information
 */
const refreshCaseViewDataOrAssignment = (reqConfig: ReqConfig, payload: RefreshPayLoad) => {
  const { context } = reqConfig;
  prepareRequestHeaders(reqConfig);
  const reqContext = prepareRequestContext(payload);
  if (hasActiveRequestsForContext(reqContext)) {
    const toBeDeletedRequests: number[] = [];
    for (let index = 0; index < reqConfigList[reqContext].length; ) {
      const request = reqConfigList[reqContext][index];
      const cancelDecision = shouldCancelRequest(request, reqConfig);
      /* istanbul ignore else */
      if (cancelDecision === 'CANCEL_REFRESH') {
        const error = new Error() as unknown as { code: string };
        error.code = 'ERR_CANCELED';
        return Promise.reject(error);
      }
      /* istanbul ignore else */
      if (cancelDecision === true) {
        toBeDeletedRequests.push(index);
        request.cancelConfig?.cancel();
      }
      index += 1;
    }
    toBeDeletedRequests.forEach((index: number) => {
      reqConfigList[reqContext].splice(index, 1);
    });
  }

  const cancelToken = getCancelTokenSource();
  reqConfig.signal = cancelToken.token;
  reqConfig.cancelConfig = cancelToken;
  if (reqConfigList[reqContext]) {
    reqConfigList[reqContext].push(reqConfig);
  } else {
    reqConfigList[reqContext] = [reqConfig];
  }
  return fetch(reqConfig.url, reqConfig, context, { cancelContext: reqContext })
    .then((response) => {
      delete reqConfigList[reqContext];
      return response;
    })
    .catch((error) => {
      if (error && error.code !== 'ERR_CANCELED') {
        delete reqConfigList[reqContext];
      }
      throw error;
    });
};

const getRestAPIAndBody = (payload: ActionPayload, body: RequestBody) => {
  const {
    caseWideAction,
    caseViewAction,
    fieldsWithDeclarativeTargets,
    fieldsWithWhen,
    isViewsWithWhen,
    interestPage,
    options
  } = payload;
  let restAPI: RestAPIPayload = {};
  const { optionalPayload } = options;
  if (optionalPayload) {
    body.content = {
      ...optionalPayload
    };
  }
  let requestBody = {
    ...body
  };
  let requestType = refreshConstants.REFRESH;
  if (caseViewAction) {
    restAPI = resolveRestApi(refreshConstants.REFRESH_CASE_VIEW, payload);
    requestBody = {
      ...requestBody,
      interestPage
    };
  } else if (isViewsWithWhen && caseWideAction) {
    restAPI = resolveRestApi(refreshConstants.REFRESH_CASE_DATA, payload);
  } else if (isViewsWithWhen && !caseWideAction) {
    restAPI = resolveRestApi(refreshConstants.REFRESH_ASSIGNMENT_DATA, payload);
  } else if (fieldsWithDeclarativeTargets.length > 0 || fieldsWithWhen.length > 0) {
    requestBody = {
      ...requestBody,
      calculations: {
        fields: fieldsWithDeclarativeTargets,
        whens: fieldsWithWhen
      }
    };
    if (caseWideAction) {
      restAPI = resolveRestApi(refreshConstants.RECALCULATE_CASE_DATA, payload);
    } else {
      restAPI = resolveRestApi(refreshConstants.RECALCULATE_ASSIGNMENT_DATA, payload);
    }
    requestType = refreshConstants.RECALCULATE;
  }
  return { restAPI, requestBody, requestType };
};

/**
 * [refreshData]
 * Description : Epic to do side effect - refreshing a view.
 * ACTION_IN -  REFRESH_DATA
 * ACTION_OUT - ERROR or MERGE_DATA
 * @param action$ (Observable<Action>)
 * @param state$ (Returns the object with headers)
 */
const refreshData = (action$: Observable<Action>, state$: { value: any }) => {
  return action$.pipe(
    ofType(refreshConstants.REFRESH_DATA, actionType.FILL_FORM_WITH_AI), // Check the action is "REFRESH_DATA" or "FILL_FORM_WITH_AI"
    map((action: Action) => {
      return action.payload;
    }), // Pass on the payload
    mergeMap((payload: any) => {
      const { context, contextData, options } = payload;
      const { propertyName = '', classID = '' } = options;
      const changeSet = getSubmitData(context, { skipUUID: false });
      let fieldsWithDeclarativeTargets: CalculatedField[] = [];
      let whens: CalculatedField[] = [];
      let isViewsWithWhen = false;

      const featureMap: { granularUIRefresh: boolean } = EnvironmentInfo.getFeatureMap() as {
        granularUIRefresh: boolean;
      };
      if (!featureMap.granularUIRefresh) {
        /**
         * This check helps to fallback refersh implementation because either the DXAPI for recalculate is not done or
         * the portal is on Launchpad as recalculate feature is not supported in Launchpad.
         * The Logic:  `isViewsWithWhen` is true means that there are `WHEN` condition configured on views in this
         * scenario we are going with the default implementation of calculating /refresh endpoint.
         */
        isViewsWithWhen = true;
      } else {
        ({ fieldsWithDeclarativeTargets = [], whens = [] } = getItemsToRecalculate(context));
        isViewsWithWhen = whens.some((val: CalculatedField) => val.type === VIEW);
      }
      const fieldsWithWhen = whens.filter((val: CalculatedField) => val.type === FIELD);
      const changedProperty = Utils.getPropertyName(propertyName);
      const propertyNameArray = changedProperty ? changedProperty.split('.') : [];
      const leafPropertyName = propertyNameArray.pop();
      const parentProperty = propertyNameArray.join('.');
      const serverPrecedenceFields: any[] = [];
      if (parentProperty) {
        const referencedMetaData = RuleStore.getPropertyMetadata(parentProperty, classID);
        // @ts-expect-error
        if (referencedMetaData && referencedMetaData.dataRetrievalType === 'copy') {
          const propertyObject = Utils.getIn(changeSet, `content.content.${parentProperty}`) || {};
          if (Object.keys(propertyObject).length > 0) {
            const removedProps = Object.keys(propertyObject).filter((val) => val !== leafPropertyName);
            removedProps.forEach((val) => {
              delete propertyObject[val];
              serverPrecedenceFields.push(`${parentProperty}`);
            });
          }
        }
      }
      payload.serverPrecedenceFields = serverPrecedenceFields;

      let body: RequestBody = {
        ...changeSet.content,
        contextData
      };

      /** merge submitData from parentContext defined on the current context containeritem */
      const parentContextChangeSet = getParentContextChangeSet(context);
      if (!Utils.isEmpty(parentContextChangeSet)) {
        body = mergeObjectsAndConcatArrays(parentContextChangeSet, body);
      }

      const restAPIAndBody = getRestAPIAndBody(
        { ...payload, fieldsWithDeclarativeTargets, fieldsWithWhen, isViewsWithWhen },
        body
      );
      ActionsSequencer.registerBlockingAction(context).catch(() => {});

      const { restAPI, requestBody, requestType } = restAPIAndBody;
      payload = {
        ...payload,
        requestType
      };
      payload.requestType = requestType;
      const { reqType, restApi } = restAPI;
      const reqHeaders = state$.value.data[context]?.caseInfo?.headers;
      const reqConfig = {
        reqHeaders,
        context,
        method: reqType,
        body: requestBody,
        url: appendQueryParamsForRefreshAPI(payload, restApi)
      };
      return from(refreshCaseViewDataOrAssignment(reqConfig, payload)).pipe(
        map((response: any) => {
          const responseJSON = response.data || {};
          return {
            response: { ...responseJSON },
            actionConfig: payload,
            actionMgrID: payload.options.actionMgrID,
            headers: processHeaders(response.headers)
          };
        }),
        logAndCatchError(
          ': Error occurred during fetch call at refreshData Epic : ',
          payload.context,
          payload?.options?.actionMgrID
        )
      );
    }), // Fetch call to REFRESH_DATA and then pass on the response
    mergeMap(async (data) => {
      if (data && data.type === actionType.ERROR) {
        ActionsSequencer.deRegisterBlockingAction(data.payload?.context).catch(() => {});
        return processDataToDispatchErrorAction(data); // If there is any error emit the ERROR action
      }
      const { actionConfig, response, actionMgrID } = data as Payload;
      const { context, serverPrecedenceFields, requestType } = actionConfig as ActionConfig;
      const vcTargetName = TargetMap.getTargetViewContainerName(actionType.ACTION_SHOWVIEW);
      const target = getResolvedTargetName(vcTargetName, context);
      const options = { acName: target, context };
      const translatedDXAPIResponse = getTranslatedDXAPIResponse(response, {
        [publicConstants.STOPMERGEACTIONS]: true,
        [publicConstants.ACTIONS.DELETEAVIALACTIONS]: true
      });
      translatedDXAPIResponse.directUrlUpdation = false;
      if (requestType === refreshConstants.REFRESH) {
        await processViewResponse(translatedDXAPIResponse, actionConfig, options);
      }
      const commonInfo = { target, context };
      const payload = getActionPayload(translatedDXAPIResponse, commonInfo);
      if (actionConfig.options?.skipDataMerge && payload.data?.caseInfo?.content) {
        payload.data.caseInfo.content = {};
      }
      payload.preserveClientChanges =
        // @ts-expect-error
        actionConfig.options.preserveClientChanges !== undefined ? actionConfig.options.preserveClientChanges : true;
      const propertiesToReplace = CaseUtils.getPropertiesToReplace(context);
      const refreshPayload = actions.postProcess(actions.mergeData, payload, {
        serverPrecedenceFields,
        propertiesToReplace
      });
      StateMachineManager.registerForServerChanges();
      StateManager.dispatch(refreshPayload);
      ActionManagerInstance.resolveActionByID(actionMgrID, payload);
      StateMachineManager.unRegisterForServerChanges();
      ActionsSequencer.deRegisterBlockingAction(context).catch(() => {});
      return [];
    })
  );
};

const refreshEpics = [refreshData];
export default refreshEpics;
