import { produce } from 'immer';

import Utils from '../../../../utils/utils';
import * as StateManager from '../../state-manager';
import * as ActionTypes from '../../actions';
import { processSharedData, setCaseViewMode } from '../helper';
import type { Action } from '../../state-manager';

const uiReducer = (state: any, action: Action) =>
  produce(state, (draft: any) => {
    switch (action.type) {
      case ActionTypes.MERGE_DATA: {
        const { propertiesToReplace = [] } = action.payload;
        draft[action.payload.context] = StateManager.mergeDeep(draft[action.payload.context], action.payload.data, {
          isArrayDeepMerge: action.payload.isArrayDeepMerge,
          propertiesToReplace: [...propertiesToReplace]
        });
        setCaseViewMode(draft, action.payload, true);
        processSharedData(draft, action.payload);
        return draft;
      }
      case ActionTypes.SET_PROPERTY: {
        const { context, reference, value, isArrayDeepMerge } = action.payload;
        StateManager.updateValue(draft[context], reference, value, { isArrayDeepMerge });
        return draft;
      }

      case ActionTypes.REPLACE_PROPERTY: {
        const { context, reference, value } = action.payload;
        StateManager.replaceValue(draft[context], reference, value);
        return draft;
      }

      case ActionTypes.REMOVE_ITEM:
        StateManager.removeItem(draft[action.payload.context], action.payload.reference);
        return draft;

      case ActionTypes.SET_LOADING:
        Utils.setIn(draft[action.payload.context], 'context_data.isLoading', true);
        return draft;

      case ActionTypes.RESET_LOADING:
        Utils.setIn(draft[action.payload.context], 'context_data.isLoading', false);
        return draft;
      case ActionTypes.ADD_SHARED_DATA: {
        const { data: sharedData } = action.payload;
        processSharedData(draft, { data: { shared: sharedData } });
        return draft;
      }
      case ActionTypes.REMOVE_SHARED_DATA: {
        const { dataPagesToBeRemoved = [] } = action.payload;
        dataPagesToBeRemoved.forEach((item: any) => {
          const { dataPageName, dataPageNameWithHash } = item;
          Utils.deleteIn(draft.shared, [dataPageName, dataPageNameWithHash]);

          const dataPage = Utils.getIn(draft.shared, [dataPageName]);
          if (dataPage && Object.keys(dataPage).length === 0) {
            Utils.deleteIn(draft.shared, [dataPageName]);
          }
        });
        return draft;
      }
      case ActionTypes.ALTER_BLOCKING_ACTION_COUNT: {
        const blockingActionCounter = Utils.getIn(
          draft[action.payload.context].context_data,
          'blocking_action_counter',
          0
        );
        if (action.payload.isInitiateBlockingAction) {
          Utils.setIn(draft[action.payload.context].context_data, 'blocking_action_counter', blockingActionCounter + 1);
        } else {
          Utils.setIn(draft[action.payload.context].context_data, 'blocking_action_counter', blockingActionCounter - 1);
        }
        return draft;
      }
      default:
        return draft;
    }
  });

export default uiReducer;
