import { C11nEnv } from '../interpreter/c11n-env';

let paragraphRuleObjC11nEnv: any;

export const resolveProperties = (match: string) => {
  /* istanbul ignore else */
  if (match) {
    const doc = new DOMParser().parseFromString(match, 'text/html');
    const property: string = doc.getElementsByTagName('pega-reference')[0].getAttribute('data-rule-id') ?? '';
    if (property) {
      const resolvedProperty = paragraphRuleObjC11nEnv.getValue(property);
      paragraphRuleObjC11nEnv.registerAdditionalProps({ [property]: `@P ${property}` });
      return resolvedProperty;
    }
  }
};

export const resolveParagraphContent = (paragraphContent: string, c11nEnv: C11nEnv) => {
  const regex = /<pega-reference(.*?)<\/pega-reference>/g;
  paragraphRuleObjC11nEnv = c11nEnv;
  const resolvedContent = paragraphContent.replace(regex, resolveProperties);

  const isEmptyParagraphTag = /^(<p>)\s*(<\/p>)$/.test(resolvedContent);
  if (isEmptyParagraphTag) {
    // If content resolves to empty paragraph tag, return null so that no visual gap shows in UI
    return null;
  }
  return resolvedContent;
};
