import { produce } from 'immer';

import { mergeDeep, updateValue } from '../../state-manager';
import * as ActionTypes from '../../actions';
import PegaConstants from '../../../../constants';
import Utils from '../../../../utils/utils';
import {
  processSharedData,
  setCaseViewMode,
  setActionTypeAndAssignmentId,
  deleteBlockingActionCounter
} from '../helper';

const deleteTransientItems = (draft: any, transientItems: any[] = []) => {
  transientItems.forEach((transientItem) => Utils.deleteIn(draft, transientItem));
};

const deleteSuggestionContexts = (draft: any, suggestionContexts: any[] = []) => {
  suggestionContexts.forEach((suggestionContext) => Utils.deleteIn(draft, suggestionContext));
};

const deleteChildContainersContexts = (draft: any, childContainersContexts: any[] = []) => {
  childContainersContexts.forEach((context) => {
    Utils.deleteIn(draft, context);
  });
};

const deleteCaseSummary = (
  draft: any,
  context: string,
  options: { deleteAssignmentSpecificResources?: boolean; doDeleteConfirmationDetails?: boolean } = {}
) => {
  const { deleteAssignmentSpecificResources = true, doDeleteConfirmationDetails = true } = options;
  Utils.deleteIn(draft, [context, 'nextAssignmentInfo']);
  Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'availableActions']);
  Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'availableProcesses']);
  Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'activeActionID']);
  Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'assignments']);
  Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'childCases']);
  if (doDeleteConfirmationDetails) {
    Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'confirmationLinks']);
    Utils.deleteIn(draft, [context, 'caseMessages']);
  }
  if (deleteAssignmentSpecificResources) {
    Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'actionButtons']);
    Utils.deleteIn(draft, [context, PegaConstants.CASE_INFO.CASE_INFO, 'navigation']);
  }
};

const deleteContainerItemContext = (draft: any, payload: any) => {
  const { containerItemID, context, deleteContext } = payload;
  if (deleteContext) {
    Utils.deleteIn(draft, [context]);
  }
  Utils.deleteIn(draft, [containerItemID]);
};

const caseReducer = (state: { [key: string]: any } | undefined, action: { type: string; payload: any }) =>
  produce(state || {}, (draft: any) => {
    switch (action.type) {
      case ActionTypes.ADD_CONTAINER_ITEM: {
        if (!action.payload.data || draft[action.payload.context]) {
          return draft;
        }

        if (action.payload.doDeleteCaseSummary) {
          deleteCaseSummary(draft, action.payload.context);
        }

        const { target } = action.payload;
        if (target && (target.indexOf(PegaConstants.WORKAREA) !== -1 || target.indexOf(PegaConstants.MODAL) !== -1)) {
          draft[action.payload.context] = action.payload.data;
        } else {
          draft[action.payload.containerItemID] = action.payload.data;
        }

        setCaseViewMode(draft, action.payload);
        setActionTypeAndAssignmentId(draft, action.payload);
        processSharedData(draft, action.payload);
        deleteBlockingActionCounter(draft, action.payload);
        return draft;
      }

      case ActionTypes.UPDATE_CONTAINER_ITEM: {
        if (action.payload.doDeleteCaseSummary) {
          deleteCaseSummary(draft, action.payload.context, {
            deleteAssignmentSpecificResources: action.payload.deleteAssignmentSpecificResources
          });
        }

        const { target, propertiesToReplace = [] } = action.payload;

        if (target && (target.indexOf(PegaConstants.WORKAREA) !== -1 || target.indexOf(PegaConstants.MODAL) !== -1)) {
          draft[action.payload.context] = mergeDeep(draft[action.payload.context], action.payload.data, {
            propertiesToReplace: [...propertiesToReplace]
          });
        } else {
          draft[action.payload.containerItemID] = mergeDeep(
            draft[action.payload.containerItemID],
            action.payload.data,
            { propertiesToReplace: [...propertiesToReplace] }
          );
        }

        if (action.payload.actionType || action.payload.assignmentID) {
          setActionTypeAndAssignmentId(draft, action.payload);
        }
        setCaseViewMode(draft, action.payload);
        processSharedData(draft, action.payload);
        deleteSuggestionContexts(draft, action.payload.suggestionContexts);
        return draft;
      }

      case ActionTypes.REMOVE_CONTAINER_ITEM: {
        const { childContainersContexts = [] } = action.payload;
        deleteContainerItemContext(draft, action.payload);
        deleteChildContainersContexts(draft, childContainersContexts);
        deleteTransientItems(draft, action.payload.transientItems);
        deleteSuggestionContexts(draft, action.payload.suggestionContexts);
        return draft;
      }

      case ActionTypes.RESET_CONTAINERS: {
        const { containerItems } = action.payload;
        if (Array.isArray(containerItems)) {
          containerItems.forEach((containerItem) => {
            Utils.deleteIn(draft, [containerItem]);
          });
        } else {
          Utils.deleteIn(draft, [containerItems]);
        }

        return draft;
      }

      case ActionTypes.UPMERGE_AND_REMOVE_CONTAINER_ITEM: {
        const { context, contextToBeRemoved, data, childContainersContextsToBeRemoved } = action.payload;
        // Remove navigation, actionButtons, nextAssignmentInfo in contextToBeRemoved as we don't need to merge this to context.
        deleteCaseSummary(draft, action.payload.context);
        setCaseViewMode(draft, action.payload);
        draft[context] = mergeDeep(draft[context], draft[contextToBeRemoved]);
        draft[context] = mergeDeep(draft[context], data);
        Utils.deleteIn(draft, [contextToBeRemoved]);
        deleteChildContainersContexts(draft, childContainersContextsToBeRemoved);
        processSharedData(draft, action.payload);
        return draft;
      }

      case ActionTypes.REPLACE_CONTAINER_ITEM: {
        const {
          payload: { context, pulseReplace = true, childContainersContexts = [] }
        } = action;
        let {
          payload: { data }
        } = action;
        const pulse = Utils.getIn(draft[context], 'pulse');
        let OperatorID = Utils.getIn(draft[context], 'OperatorID');
        let pxRequestor = Utils.getIn(draft[context], 'pxRequestor');

        if (pxRequestor && data.pxRequestor) pxRequestor = Utils.mergeDeep(pxRequestor, data.pxRequestor);
        if (OperatorID && data.OperatorID) OperatorID = Utils.mergeDeep(OperatorID, data.OperatorID);

        if (!pulseReplace && pulse) {
          data = updateValue(data, 'pulse', pulse);
          data = updateValue(data, 'OperatorID', OperatorID);
          data = updateValue(data, 'pxRequestor', pxRequestor);
        }
        Utils.setIn(draft, context, data);
        setCaseViewMode(draft, action.payload);
        processSharedData(draft, action.payload);
        deleteChildContainersContexts(draft, childContainersContexts);
        deleteTransientItems(draft, action.payload.transientItems);
        deleteSuggestionContexts(draft, action.payload.suggestionContexts);
        return draft;
      }

      case ActionTypes.DELETE_CASE_SUMMARY: {
        const { context, doDeleteConfirmationDetails = true } = action.payload;
        deleteCaseSummary(draft, context, { doDeleteConfirmationDetails });
        return draft;
      }

      case ActionTypes.ADD_TRANSIENT_ITEM: {
        const { transientItemID, data } = action.payload;
        return Utils.setIn(draft, transientItemID, data);
      }

      case ActionTypes.REMOVE_TRANSIENT_ITEM:
      case ActionTypes.REMOVE_ALL_TRANSIENT_ITEMS: {
        const { transientItems } = action.payload;
        transientItems.forEach((transientItem: any) => Utils.deleteIn(draft, transientItem));
        return draft;
      }

      case ActionTypes.UPDATE_TRANSIENT_DATA:
      case ActionTypes.CLEAR_TRANSIENT_DATA: {
        const { transientItemID, data } = action.payload;
        mergeDeep(Utils.getIn(draft, transientItemID), data);
        return draft;
      }

      case ActionTypes.PURGE_TRANSIENT_DATA: {
        const { transientItemID, data } = action.payload;
        for (const key in data) {
          if (Object.prototype.hasOwnProperty.call(data, key)) {
            Utils.deleteIn(draft, [transientItemID, key]);
          }
        }
        return draft;
      }

      case ActionTypes.REPLACE_TRANSIENT_DATA: {
        const { transientItemID, data } = action.payload;
        Utils.deleteIn(draft, transientItemID);
        return Utils.setIn(draft, transientItemID, data);
      }

      case ActionTypes.SET_SHARED_PROPERTY: {
        const { key, value } = action.payload;
        Utils.setIn(draft, `${PegaConstants.SHARED}.${key}`, value);
        return draft;
      }

      case ActionTypes.ADD_SUGGESTION_CONTEXT: {
        const { context, data } = action.payload;
        return Utils.setIn(draft, context, data);
      }

      default:
        return draft;
    }
  });

export default caseReducer;
