import AssetLoader from '../../utils/asset-loader';
import { localeUtils } from '../../locale/locale-utils';
import EnvironmentInfo from '../../environment-info';
import { invokeRestApi } from '../../rest-client';

const importLocalizationAssets = async (localeRule: string | undefined, userLocale: string | undefined) => {
  const localeRes = await AssetLoader.getSvcLocale(userLocale, `${localeRule}.json`);
  if (localeRes?.ok && localeRule) {
    const localeJson = await localeRes.json();
    localeUtils.setLocaleForRule(localeJson, localeRule);
  }
};

const importLocalizationV2Assets = async (localeRule: string | undefined) => {
  const availableLocales = EnvironmentInfo.getFeatureMap().availableLanguagePacks || [];
  const ifSupportedLocale = localeRule && availableLocales.includes(localeRule);
  if (!ifSupportedLocale) {
    console.warn(`current locale ${localeRule} is not supported by application.`);
    return;
  }
  const queryPayload = {
    locale: localeRule
  };
  const localeRes = await invokeRestApi('getLocalizationPack', { queryPayload });
  if ((localeRes.status === 201 || localeRes.status === 200) && localeRes.data) {
    const localeJson = localeRes.data;
    Object.keys(localeJson).forEach((key) => {
      localeUtils.setLocaleForRule(localeJson[key], key);
    });
  }
};

const loadLocalizationAssets = async () => {
  // Get the generic locale bundle
  const userLocale = EnvironmentInfo.getLocale();
  const localizationVersion = EnvironmentInfo.getFeatureMap().localizationVersion || 1;
  let isCurrentLocalePackAvailable: string | boolean | undefined;
  if (userLocale !== 'en-US') {
    try {
      let localePack;
      if (localizationVersion === 2) {
        localePack = [userLocale];
      } else {
        localePack = [
          localeUtils.GENERIC_BUNDLE_KEY,
          localeUtils.MESSAGE_BUNDLE_KEY,
          localeUtils.FIELD_LABELS_BUNDLE_KEY,
          ...localeUtils.getCaseTypeLocaleReferences()
        ];
        isCurrentLocalePackAvailable = await localeUtils.isCurrentLocalePackAvailable();
      }
      const localePromises = localePack.map(async (localeRule) => {
        if (localizationVersion === 2) {
          await importLocalizationV2Assets(localeRule);
        } else if (isCurrentLocalePackAvailable) {
          await importLocalizationAssets(localeRule, userLocale);
        } else {
          // No action
        }
      });
      await Promise.allSettled(localePromises);
    } catch (e) {
      console.warn(e);
    }
  }
};

export default loadLocalizationAssets;
