import constants from '../../constants';
import Validator from '../validator';
import { localeUtils } from '../../locale/locale-utils';

setTimeout(() => {
  const INVALID_PHONE_MESSAGE = localeUtils.getLocaleValue('Enter a valid phone number', 'Messages');
  import(/* webpackChunkName: "531" */ 'libphonenumber-js').then(({ parsePhoneNumberFromString }) => {
    Validator.register('phone', (value: string) => {
      let message = null;
      const phoneMatcher = /^\+?\d+$/;
      if (value && !phoneMatcher.test(value)) {
        message = INVALID_PHONE_MESSAGE;
      }
      const hasCountryCode = value?.startsWith('+');
      if (value && hasCountryCode) {
        const phoneNumber = parsePhoneNumberFromString(value);
        if (!phoneNumber?.isPossible() || value !== phoneNumber?.number) {
          message = INVALID_PHONE_MESSAGE;
        }
      }
      return message;
    });
  });
}, constants.PRIORITY.TRIVIAL);
