import { C11nEnv } from '../../interpreter/c11n-env';
import type { GenAICoachModel } from '../../rules/types';
import AnnotationsHandler from '..';
import * as RuleStore from '../../rules/rules-store';

AnnotationsHandler.registerHandler('@GENAICOACH', (genAICoachName: string, c11nEnv: C11nEnv): GenAICoachModel | null =>
  RuleStore.getGenAICoach(genAICoachName, c11nEnv.getCurrentClassID())
);
