import { getNormalizedPropertyName } from '../utils/form-handler';
import Utils from '../utils/utils';
import CascadeManager from '../cascade/manager';
import FormUtils from '../form/form-utils';
import ContextTreeManager from '../contextree/treemanager';

import type StateMachine from './state-machine/state-machine';

class StateMachineManager {
  private states: { [key: string]: any };
  private unConnectedStates: { [key: string]: any };
  constructor() {
    /* states : {
      'app/primary/workarea1': {
        'caseinfo.content.x.employeeList' : StateMachine,
        'caseinfo.content.y.employeeList' : StateMachine
      }
    } */
    this.states = {};
    this.unConnectedStates = {};
  }

  getStateMachine(context: string, pageReference: string, target: string) {
    return (
      this.states?.[context]?.[`${pageReference}${target}`] ||
      this.unConnectedStates?.[context]?.[`${pageReference}${target}`]
    );
  }

  isStateMachineExist(context: string, pageReference: string, target: string) {
    return (
      !!this.states?.[context]?.[`${pageReference}${target}`] ||
      !!this.unConnectedStates?.[context]?.[`${pageReference}${target}`]
    );
  }

  getUnConnectedStateMachines(context: string) {
    return this.unConnectedStates?.[context];
  }

  getAllStateMachines(context: string, pageReference: string, target: string) {
    return {
      states: this.states?.[context]?.[`${pageReference}${target}`],
      unConnectedStates: this.unConnectedStates?.[context]?.[`${pageReference}${target}`]
    };
  }

  getAllStateMachinesByContext(context: string) {
    return {
      states: this.states?.[context],
      unConnectedStates: this.unConnectedStates?.[context]
    };
  }

  setStateMachine(context: string, target: string, pageReference: string, stateMachine: StateMachine) {
    const targetPage = `${pageReference}${target}`;
    const { stateMachine: parentStateMachine } = this.getStateMachineObjByPath(targetPage, context, true);
    const updateStates = (key: string, value?: StateMachine, isConnectedStateType: boolean = false) => {
      const statesObj = isConnectedStateType ? this.states : this.unConnectedStates;
      /* istanbul ignore else */
      if (!statesObj[context]) {
        statesObj[context] = {};
      }
      /* istanbul ignore else */
      if (!statesObj[context][key]) {
        statesObj[context][key] = {};
      }
      statesObj[context][key] = value;
    };
    if (parentStateMachine && parentStateMachine.targetPageReference !== stateMachine.targetPageReference) {
      parentStateMachine.addChild(stateMachine);
      updateStates(targetPage, stateMachine);
      return;
    }

    const matchedChildStateMachines = Object.entries(this.states[context] || {})
      .filter(([key]) => {
        if (key.includes(targetPage)) {
          const matchedKeyStringLength = key.substring(0, targetPage.length).length;
          return (
            key.substring(matchedKeyStringLength).startsWith('[') ||
            key.substring(matchedKeyStringLength).startsWith('.')
          );
        }
        return false;
      })
      .reduce((prev, [key, value]) => ({ ...prev, [key]: value }), {});
    // Check if any child target exists in connected states and move them to unconnected states
    if (Object.keys(matchedChildStateMachines)?.length) {
      Object.entries(matchedChildStateMachines).forEach(([key, value]) => {
        delete this.states[context][key];
        stateMachine.addChild(value as unknown as StateMachine);
        updateStates(key, value as unknown as StateMachine);
      });
      this.states[context][targetPage] = stateMachine;
    } else {
      updateStates(targetPage, stateMachine, true);
    }
  }

  buildPageInstructionsByContext(context: string | null, isOnlyChanged?: boolean) {
    const states = this.getStateMachinesByContext(context) || {};
    const instructions: any[] = [];
    Object.keys(states)
      .sort()
      .forEach((target) => {
        const targetNode = ContextTreeManager.getContextReferences(context)?.get(target);
        const stateMachine = states[target];
        if (targetNode) {
          const inst = stateMachine.buildSubmitPageInstructions(isOnlyChanged);
          instructions.push(...inst);
        }
      });
    return instructions;
  }

  buildClientChangeList(context: string) {
    const states = this.getStateMachinesByContext(context) || {};
    let changeList = {};
    const resultObject = {};
    Object.keys(states).forEach((target) => {
      const stateMachine = states[target];
      const inst = stateMachine.getChangeList();
      changeList = { ...changeList, ...inst };
    });
    Utils.flatten(changeList, '', resultObject);
    return resultObject;
  }

  getStateMachinesByContext(context: string | null) {
    return context ? this.states?.[context] : undefined;
  }

  clearStateMachines(context: string) {
    this.states[context] = {};
    this.unConnectedStates[context] = {};
  }

  setStateMachineEmpty(context: string, target: string) {
    const stateMachine = this.getStateMachine(context, target, '');
    stateMachine.root = null;
    stateMachine.current = null;
    const unConnectedStateMachines = this.getUnConnectedStateMachines(context) || {};
    Object.keys(unConnectedStateMachines).forEach((key) => {
      /* istanbul ignore else */
      if (key.includes(target)) {
        delete unConnectedStateMachines[key];
      }
    });
  }

  removeStateMachine(context: string, target: string) {
    delete this.states[context]?.[target];
  }

  getStateMachineObjByPath(stateMachineTarget: string, context: string, processList: boolean) {
    const regex = /\[\d+\]$/gm;
    let index = null;
    while (stateMachineTarget) {
      if (this.isStateMachineExist(context, '', stateMachineTarget)) {
        break;
      }
      index = null;
      if (processList && stateMachineTarget.endsWith(']')) {
        index = stateMachineTarget.match(regex);
        index = index?.[index.length - 1]?.substring(1, index[index.length - 1].length - 1);
        [stateMachineTarget] = stateMachineTarget.split(regex);
      } else {
        stateMachineTarget = stateMachineTarget.substring(0, stateMachineTarget.lastIndexOf('.'));
      }
    }
    if (!stateMachineTarget) {
      return {};
    }

    return {
      stateMachineTarget,
      stateMachine: this.getStateMachine(context, '', stateMachineTarget),
      index
    };
  }

  executeStateMachine(
    context: string,
    propertyPath: string,
    value: any,
    processList = true,
    options: { skipStateUpdate?: boolean } = {}
  ) {
    const { stateMachineTarget, index } = this.getStateMachineObjByPath(propertyPath, context, processList);
    if (!stateMachineTarget) {
      return false;
    }

    let [, contentKey] = propertyPath.split(stateMachineTarget);
    if (!contentKey) {
      return false;
    }
    if (index !== null) {
      [, contentKey] = contentKey.split(/^\[\d+\]/gm);
    }
    const payload = {
      [getNormalizedPropertyName(contentKey)]: value
    };
    const stateMachine = this.getStateMachine(context, '', stateMachineTarget);
    if (index !== null) {
      stateMachine.update(payload, Number(index) + 1, options.skipStateUpdate ?? true, options);
    } else {
      stateMachine.updatePage(Utils.unflatten(payload));
    }

    return true;
  }

  /**
   * This will help us to subscribe a callback when pageList property is update in the redux because of server changes.
   * @private
   */
  registerForServerChanges() {
    CascadeManager.registerForListChanges((context: string, propertyReference: string, value: any) => {
      /*
      we are not interested to generate update instruction on change of classID because we shouldn't pass classID in the
      page instructions request body while submit.
      When adding a new row in pyRefreshData, generating UPDATE Page Instructions fails due to delayed Context Node creation.
      To address this, we've added a setTimeout to ensure the Context Node is ready before checking FormUtils.isPropertyEditable
      and generating the instructions.
      TODO: We have to explore for more sophisticated approach to eliminate setTimeout.
      */
      setTimeout(() => {
        const splitForMultiselect = '].';
        if (!propertyReference.includes('.classID') && FormUtils.isPropertyEditable(context, propertyReference)) {
          this.executeStateMachine(context, propertyReference, value);
        }
        if (!propertyReference.includes('.classID') && propertyReference.includes(splitForMultiselect)) {
          const parentPageListName = propertyReference.substring(0, propertyReference.lastIndexOf('['));
          const editableFields = FormUtils.getEditableFields(context);
          if (
            ['multiselect', 'checkbox'].includes(
              editableFields.find((fieldObject) => fieldObject.name === parentPageListName)?.type
            )
          ) {
            const formFieldMap = ContextTreeManager.getContextReferences(context);
            const fieldNode = formFieldMap?.get(parentPageListName);
            const propertyName = propertyReference.substring(
              propertyReference.lastIndexOf(splitForMultiselect) + splitForMultiselect.length
            );
            if (fieldNode?.props.selfReferencingDatapageKeys?.includes(propertyName)) {
              this.executeStateMachine(context, propertyReference, value);
            }
          }
        }
      }, 0);
    });
  }

  /**
   * This will help us to un-subscribe all the existing callbacks.
   * @private
   */
  unRegisterForServerChanges() {
    CascadeManager.unRegisterForListChanges();
  }
}
export default new StateMachineManager();
