import AnnotationsHandler from '..';
import { C11nEnv } from '../../interpreter/c11n-env';
import ParagraphUtils from '../../dynamic-content/util';

AnnotationsHandler.registerHandler('@PARAGRAPH', (paragraphName: string, c11nEnv: C11nEnv) => {
  const paragraphContent = c11nEnv.getLocalizedParagraphValue(paragraphName);

  return paragraphContent ? ParagraphUtils.getParagraphStream(paragraphContent, c11nEnv) : null;
});
