import type { NodeProps } from '../../../contextree/types';

import { supportedGrammar, expressionHandlers } from './definitions';
import Parser from './parser';
import Lexer from './lexer';

/**
 * [Expression]
 * Description : Creates an instance for the flat expression string on which different actions
 * can be performed which are part of the evaluation process.
 * @class
 */
class Expression {
  private readonly _grammar: any;
  private readonly _expression: any;
  private _ast: any | null;
  private _context: any;
  private _contextTreeNodeContext?: NodeProps | null;
  private readonly _localDataObject: any;

  /**
   * Description : Expression instance of a flat expression string.
   * @class
   * @param expression Expression string.
   * @param localData data
   */
  constructor(expression: string, localData: any) {
    this._grammar = { ...supportedGrammar };
    this._expression = expression;
    this._ast = null;
    this._context = {};
    this._contextTreeNodeContext = null;
    this._localDataObject = localData;
  }

  /**
   * [buildAST]
   * Description : Builds the Abstract Syntax Tree for an expression.
   * @returns Returns the updated instance with the AST.
   */
  buildAST() {
    const lexer = new Lexer(this._grammar);
    const tokens = lexer.tokenize(this._expression);
    const parser = new Parser(this._grammar);
    parser.addTokens(tokens);
    this._ast = parser.buildAst();
    return this;
  }

  /**
   * [evaluate]
   * Description : Evaluates the AST of the expression and returns the result.
   * @param context     c11n object
   * @returns Evaluation result
   */
  evaluate(context: object | undefined, contextTreeNodeProps?: NodeProps) {
    this._context = context;
    this._contextTreeNodeContext = contextTreeNodeProps;
    return this.evaluateAST(this._ast);
  }

  /**
   * [evaluateAST]
   * Description : Evaluates the AST of the expression and returns the result.
   * @param ast Abstract syntax tree
   * @returns Evaluation result
   */
  evaluateAST(ast: any, leftType?: string): boolean {
    return expressionHandlers[ast.operationType].call(this, ast, leftType);
  }
}

export default Expression;
