import { C11nEnv } from '../interpreter/c11n-env';

import { resolveParagraphContent } from './parser';

class ParagraphUtils {
  static getParagraphStream(paragraphRuleObj: string, c11nEnv: C11nEnv) {
    return resolveParagraphContent(paragraphRuleObj, c11nEnv);
  }
}

export default ParagraphUtils;
