import type { CancelTokenSource } from 'axios';

import { invokeRestApi, getCancelTokenSource } from '../rest-client';

/**
 * Use the API in the GenAIAssistantUtils module to manage GenAI functionalities.
 * @module GenAIAssistantUtils
 */
const CREATE_CONVERSATION = 'createConversation';
const SEND_MESSAGE = 'sendMessage';

type Prompt = {
  pxObjClass: string;
  pyInstructions: string;
  pyPrompt: string;
};

type Response = {
  ID: string;
  response: string;
  suggestedPrompts: Prompt[];
  welcomeMessage: string;
  initialInstruction: string;
};

type ConversationResponse = {
  data: Response;
};

/**
 * Initiates a conversation for a specified GenAI Assistant rule.
 * @function
 * @static
 * @example <caption>In this example, the API creates a conversation with a GenAI Assistant rule for a case.</caption>
 * PCore.getGenAiAssistantUtils().createConversation(
 * 'UPLUS-PLUS-WORK-OPPORTUNITY OPP-15001',
 * 'UPlus-SAPlusC11n-Work-Opportunity-Biz!OpportunityAssistant',
 * [], // Pass the reference of the array.
 * 'app/primary_1'
 * )
 * .then(response => {
 *    // response
 * }).catch(err => {
 *   // errors
 * })
 * @param   {string}              contextID         The ID of the context where the conversation must be initiated. Note: If the context is a case, ensure that you provide the pzInsKey value of the case.
 * @param   {string}              assistantID       The unique identifier of the GenAI Assistant rule with which the conversation must be initiated. Note: Ensure that you provide the pxInsName value of the assistant rule.
 * @param   {string}              context           The name of the context where the API is being called.
 * @param   {CancelTokenSource[]} cancelTokenSource The list of tokens of API requests for creating the conversation. Note: `cancelTokenSource` is used to cancel the previous request when a new one is triggered.
 * @returns {Promise<object>}     A Promise that resolves to an object containing the details of the conversation with the GenAI Assistant rule.
 */
const createConversation = (
  contextID: string,
  assistantID: string,
  context: string,
  cancelTokenSource: CancelTokenSource[] = []
) => {
  // Cancel any ongoing requests from previous CancelTokenSources
  for (const token of cancelTokenSource) {
    /* istanbul ignore else */
    if (token?.cancel && typeof token.cancel === 'function') {
      token.cancel();
    }
  }

  const newCancelTokenSource = getCancelTokenSource();
  cancelTokenSource.push(newCancelTokenSource);
  const queryPayload = {
    assistantID
  };
  const reqBody = JSON.stringify({
    contextID
  });
  return invokeRestApi(
    CREATE_CONVERSATION,
    { body: reqBody, cancelTokenSource: newCancelTokenSource, queryPayload },
    context
  ).then((response: ConversationResponse) => {
    cancelTokenSource.pop();
    return response;
  });
};

/**
 * Sends a message in an existing conversation with the GenAI Assistant.
 * @function
 * @static
 * @example <caption>In this example, the API sends a message in an existing conversation with a GenAI Assistant.</caption>
 * PCore.getGenAiAssistantUtils().sendMessage('UPlus-SAPlusC11n-Work-Opportunity-Biz!OpportunityAssistant','CON-38008','test message','app/primary_1')
 * .then(response => {
 *    // response
 * }).catch(err => {
 *   // errors
 * });
 * @param   {string}  assistantID     The unique identifier of the GenAI Assistant rule with which the conversation must be initiated. Note: Ensure that you provide the pxInsName value of the assistant rule.
 * @param   {string}  conversationID  The unique identifier of the object that contains the details of the conversation.
 * @param   {string}  message         The message that must be sent to the assistant.
 * @param   {string}  context         The name of the context where the API is being called.
 * @returns {Promise<object>}         A Promise that resolves to an object containing the response from the GenAI Assistant.
 */
const sendMessage = (assistantID: string, conversationID: string, message: string, context: string) => {
  const queryPayload = {
    assistantID,
    conversationID
  };
  const reqBody = JSON.stringify({
    request: message
  });
  return invokeRestApi(SEND_MESSAGE, { body: reqBody, queryPayload }, context);
};

export default {
  createConversation,
  sendMessage
};
