import { getDataObject, getValue } from '../store/state/state-manager';
import StateMachineManager from '../page-instructions/state-machine-manager';
import { publicConstants } from '../constants';
import ContextProcessor from '../context/context-processor';
import ContextTreeManager from '../contextree/treemanager';
import Validator from '../validation/validator';
import type { NodeProps } from '../contextree/types';
import type TreeNode from '../contextree/treenode';

import Utils from './utils';
import type { EditableFormFieldOptions, FormFieldOptions } from './types';

const formPageFieldsMap: { [key: string]: any } = {};

/**
 * Returns the property name without period if it exists
 * @param propertyName property name to be normalized
 */
export const getNormalizedPropertyName = (propertyName: string) => {
  return propertyName.startsWith('.') ? propertyName.substring(1) : propertyName;
};

/**
 * Removes the remove FormFields from the formFieldsMap when
 * container item get deleted
 * @param contextName context name to be removed from object
 */
export const removeFormFieldsFromMap = (contextName: string) => {
  delete formPageFieldsMap[contextName];
};

export const addPageFieldToForm = (contextName: string, propertyName: string, value: string) => {
  const propName = getNormalizedPropertyName(propertyName);
  if (formPageFieldsMap[contextName]) {
    formPageFieldsMap[contextName].fieldOrder[propName] = value;
  } else {
    formPageFieldsMap[contextName] = {
      fieldOrder: {
        [propName]: value
      }
    };
  }
};
const isPageListField = (contextTreeNodeProps: NodeProps) => {
  return (
    contextTreeNodeProps.selectionList ||
    contextTreeNodeProps.selectionKey ||
    (contextTreeNodeProps.renderMode === 'Editable' && contextTreeNodeProps.referenceList)
  );
};

const isFieldEditable = (contextTreeNodeProps: NodeProps, options: FormFieldOptions = {}) => {
  const { includeDisabledFields = false } = options;
  const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(contextTreeNodeProps);
  /* check readOnly, visibility, disabled */
  const isEditable =
    resolvedLiteProps.readOnly !== true &&
    resolvedLiteProps.visibility !== false &&
    (includeDisabledFields || resolvedLiteProps.disabled !== true);
  const isFilteredListField =
    typeof contextTreeNodeProps?.value === 'string' && contextTreeNodeProps?.value.includes('@FILTERED_LIST');
  const isPageListProperty = isPageListField(contextTreeNodeProps);
  return (
    /* selectionList field OR Editable referenceList */
    (contextTreeNodeProps?.value || isPageListProperty) &&
    /* FilteredList field */
    !isFilteredListField &&
    /* check  readOnly , visibility ,disabled */
    isEditable
  );
};

const pushAttachments = (
  attachmentList: object[],
  fieldProperty: string | undefined,
  context: string,
  resolvedLiteProps: NodeProps,
  key: string
) => {
  let attachmentsInfo = (
    getValue(`.attachmentsList.${fieldProperty?.replace('.', '')}`, 'context_data', context) || []
  ).filter((f: { label: string }) => {
    return `${resolvedLiteProps.pageReference}.${f.label}` === key;
  });
  attachmentsInfo = Utils.buildAttachmentsFormData(attachmentsInfo);
  if (attachmentsInfo.length > 0) {
    attachmentList = [...attachmentList, ...attachmentsInfo];
  }
  return attachmentList;
};

const pushEditableFormFieldsReference = (
  context: string,
  fieldProperty: string | undefined,
  resolvedLiteProps: object,
  key: string,
  editableFormFieldsReferenceList: string[]
) => {
  const isExecuted = StateMachineManager.executeStateMachine(
    context,
    key,
    ContextProcessor.getLitePropertyValue(resolvedLiteProps, fieldProperty)
  );
  /* istanbul ignore else */
  if (!isExecuted) {
    editableFormFieldsReferenceList.push(key);
  }
  return editableFormFieldsReferenceList;
};

const isAttachmentWithInEmbedProperty = (resolvedLiteProps: NodeProps) => {
  const embeddedProperty = resolvedLiteProps.pageReference
    ?.replace(publicConstants.CASE_INFO.CASE_INFO_CONTENT, '')
    ?.replace(publicConstants.DATA_INFO.DATA_INFO_CONTENT, '');
  return resolvedLiteProps.componentName === 'Attachment' && embeddedProperty !== '';
};

const isScalarField = (key: string, resolvedLiteProps: NodeProps, options: EditableFormFieldOptions) => {
  const isInsideList = resolvedLiteProps.isInsideList;
  const { ignoreFieldsInsideList = true } = options;
  return (
    (!isInsideList || (isInsideList && !ignoreFieldsInsideList)) &&
    !Array.isArray(ContextProcessor.getLitePropertyValue(resolvedLiteProps, key)) &&
    resolvedLiteProps.componentName !== 'Address'
  );
};

const getAttachmentsAndFormFieldsList = (
  formFieldsMap: Map<string, TreeNode>,
  context: string,
  options: EditableFormFieldOptions
) => {
  const { includeDisabledFields = false } = options;
  let editableFormFieldsReferenceList: string[] = [];
  let attachmentList: object[] = [];
  for (const [key, fieldNode] of formFieldsMap) {
    const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(fieldNode.props);
    const fieldProperty = resolvedLiteProps.propertyName as string;
    if (isFieldEditable(fieldNode.props, { includeDisabledFields })) {
      const isAttachmentInEmbedProp = isAttachmentWithInEmbedProperty(resolvedLiteProps);
      if (resolvedLiteProps.componentName === 'Attachment' && !isAttachmentInEmbedProp) {
        attachmentList = pushAttachments(attachmentList, fieldProperty, context, resolvedLiteProps, key);
      } else if (!isAttachmentInEmbedProp && isScalarField(key, resolvedLiteProps, options)) {
        editableFormFieldsReferenceList = pushEditableFormFieldsReference(
          context,
          fieldProperty,
          resolvedLiteProps,
          key,
          editableFormFieldsReferenceList
        );
      }
    }
  }
  return {
    attachmentList,
    editableFormFieldsReferenceList
  };
};

export const getEditableFormFieldsReferenceList = (context: string, options: EditableFormFieldOptions = {}) => {
  let editableFormFieldsReferenceList: string[] = [];
  let attachmentList: object[] = [];
  const formPageFieldsList: string[] = [];
  const formFieldsMap = ContextTreeManager.getContextReferences(context);
  if (!formFieldsMap) {
    return {
      formInputs: editableFormFieldsReferenceList,
      attachments: attachmentList,
      formPageFields: formPageFieldsList
    };
  }
  ({ attachmentList, editableFormFieldsReferenceList } = getAttachmentsAndFormFieldsList(
    formFieldsMap,
    context,
    options
  ));
  if (formPageFieldsMap[context]) {
    for (const key in formPageFieldsMap[context].fieldOrder) {
      /* istanbul ignore else */
      if (Object.hasOwn(formPageFieldsMap[context].fieldOrder, key)) {
        formPageFieldsList.push(key);
      }
    }
  }
  return {
    formInputs: editableFormFieldsReferenceList,
    attachments: attachmentList,
    formPageFields: formPageFieldsList
  };
};

export const getAttachmentsList = (context: string) => {
  let attachmentList: object[] = [];
  const formFieldMap = ContextTreeManager.getContextReferences(context);
  if (formFieldMap) {
    for (const [key, fieldNode] of formFieldMap) {
      const fieldProps = fieldNode?.props;
      if (isFieldEditable(fieldProps) && fieldProps?.componentName === 'Attachment') {
        const allowMultiple = fieldProps?.allowMultiple === 'true';
        attachmentList = [...attachmentList, { type: 'File', allowMultiple, fullPath: key }];
      }
    }
  }
  return attachmentList;
};
const buildSubmitPayload = (
  formPageFields: string[],
  data: { [key: string]: string | number | boolean },
  resultObj: { [key: string]: string | number | boolean }
) => {
  formPageFields.forEach((propRef) => {
    let valueObj = Utils.getIn(data, propRef);
    /* istanbul ignore else */
    if (typeof valueObj === 'object') {
      // deepClone the object to avoid Readonly object errors when setting properties inside the object.
      valueObj = Utils.deepClone(valueObj);
      delete valueObj?.classID;
    }
    Utils.buildObject(propRef, resultObj, valueObj);
  });
};

/**
 * this function provides the values for the formfields present in the given context
 * @param context - context of form
 * @param options parameters like ignoreFieldsInsideList
 * @returns - contentObject
 */
export const getFormContent = (context: string, options: EditableFormFieldOptions = {}) => {
  const resultObj = {};
  const data = getDataObject(context);
  const { ignoreFieldsInsideList = true, isTransientContext = false, includeDisabledFields = false } = options;
  if (isTransientContext || data?.caseInfo?.content) {
    const { formInputs, attachments, formPageFields } = getEditableFormFieldsReferenceList(context, {
      ignoreFieldsInsideList,
      includeDisabledFields
    });
    if (attachments && attachments.length > 0) {
      Utils.buildObject('caseInfo.attachments', resultObj, attachments);
    }
    buildSubmitPayload(formPageFields, data, resultObj);
    /* istanbul ignore else */
    if (formInputs && formInputs.length > 0) {
      formInputs.forEach((propRef) => {
        Utils.buildObject(propRef, resultObj, Utils.getIn(data, propRef));
      });
    } else if (!isTransientContext) {
      Utils.buildObject(publicConstants.CASE_INFO.CASE_INFO_CONTENT, resultObj, {});
    } else {
      // not reachable
    }
  }
  if (!isTransientContext && data?.dataInfo?.content) {
    const { formInputs } = getEditableFormFieldsReferenceList(context, { ignoreFieldsInsideList });
    formInputs.forEach((propRef) => {
      Utils.buildObject(propRef, resultObj, Utils.getIn(data, propRef));
    });
  }
  return resultObj;
};

export const getResolvedContextTreeNodeProps = (context: string, pageReference: string, propertyName?: string) => {
  const propertyReference =
    pageReference === '' && propertyName?.startsWith('.')
      ? propertyName.substring(1, propertyName.length)
      : propertyName;
  const props = ContextTreeManager.getContextReferences(context)?.get(`${pageReference}${propertyReference}`)?.props;
  if (props) {
    return ContextProcessor.resolveContextTreeNodeProps(props);
  }
  return props;
};

/**
 * Checks whether form is valid or not
 * based on the validation values for form fields
 * @param context context of form fields to be validated
 * @returns boolean
 */
export const isFormValid = (context: string | null, pageReference?: string | null) => {
  const formFieldMap = ContextTreeManager.getContextReferences(context);
  let isValid = true;
  /* istanbul ignore else */
  if (!formFieldMap) {
    return isValid;
  }
  for (const [key, fieldNode] of formFieldMap) {
    const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(fieldNode.props);
    /* istanbul ignore next */
    if (isFieldEditable(fieldNode.props) && !(pageReference && !key.includes(pageReference))) {
      const messageObject = Validator.validate(
        resolvedLiteProps.componentName?.toLowerCase(),
        resolvedLiteProps.propertyName,
        ContextProcessor.getLitePropertyValue(resolvedLiteProps, resolvedLiteProps.propertyName),
        resolvedLiteProps
      );
      if (isValid) {
        isValid = !messageObject.message;
      }
    }
  }
  return isValid;
};

/**
 * Checks whether form has active suggestion
 * @private
 * @param context context of form fields
 * @returns boolean
 */
export const isFormHasSuggestions = (context: string | null) => {
  const formFieldMap = ContextTreeManager.getContextReferences(context);
  if (formFieldMap) {
    for (const [, fieldNode] of formFieldMap) {
      const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(fieldNode.props);
      /* istanbul ignore else */
      if (resolvedLiteProps.hasSuggestions) {
        return true;
      }
    }
  }
  return false;
};

/**
 * returns all the fields in the form which are visible
 * @param context context
 */
export const getVisibleFormFields = (context: string, includeFullPath: boolean = false) => {
  const formFieldMap = ContextTreeManager.getContextReferences(context);
  const fields = [];
  if (formFieldMap) {
    for (const [key, fieldNode] of formFieldMap) {
      const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(fieldNode.props);
      /* istanbul ignore else */
      if (resolvedLiteProps.visibility !== false) {
        const startIndex = 8;
        const modifiedKey = includeFullPath ? key : key.substring(key.indexOf('content') + startIndex);
        fields.push(modifiedKey);
      }
    }
  }
  return fields;
};

/**
 * returns all the fields in the form which are readonly
 * @param context context
 */
export const getReadOnlyFormFields = (context: string) => {
  const formFieldMap = ContextTreeManager.getContextReferences(context);
  const fields = [];
  if (formFieldMap) {
    for (const [key, fieldNode] of formFieldMap) {
      const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(fieldNode.props);
      /* istanbul ignore else */
      if (resolvedLiteProps.readOnly === true) {
        fields.push(key);
      }
    }
  }
  return fields;
};

/**
 * returns all the fields in the form which are editable
 * @param context context
 * @returns editable fields information in an array -
[{
    "name": "<field full path>",
    "label": "<field label>",
    "type": "<field type>"
}]
 */
export const getEditableFields = (context: string) => {
  const editableFormFields = [];
  const formFieldMap = ContextTreeManager.getContextReferences(context);
  if (formFieldMap) {
    for (const [key, fieldNode] of formFieldMap) {
      const resolvedLiteProps: NodeProps = ContextProcessor.resolveContextTreeNodeProps(fieldNode.props);
      if (
        isFieldEditable(fieldNode.props) &&
        resolvedLiteProps.componentName !== 'Attachment' &&
        resolvedLiteProps.type !== 'multirecordlist'
      ) {
        editableFormFields.push({
          name: key,
          label: resolvedLiteProps.label,
          type: resolvedLiteProps.componentName?.toLowerCase()
        });
      }
    }
  }
  return editableFormFields;
};
