import Validator from '../validator';
import { getValue } from '../../store/state/state-manager';
import type { NodeProps } from '../../contextree/types';
import type { FileObject } from '../../attachment/types';
import { localeUtils } from '../../locale/locale-utils';

export type ValidatorOptions = Pick<NodeProps, 'target' | 'required' | 'pageReference'> & {
  context: string;
  property: string;
};
Validator.register('attachment', (_value: object, validatorOptions: ValidatorOptions) => {
  const attachments: FileObject[] =
    getValue(
      `.attachmentsList${validatorOptions.pageReference?.replace('caseInfo.content', '')}${validatorOptions.property}`,
      'context_data',
      validatorOptions.context
    ) || [];

  if (attachments.some((element) => element.props?.error)) {
    return localeUtils.getLocaleValue('Error with one or more files');
  }

  return null;
});
