import { from, of } from 'rxjs';
import { map, mergeMap } from 'rxjs/operators';

import { logAndCatchError } from '../../utils/rxjs-utils';
import { resolveRestApi } from '../../router/app-router';
import { fetch } from '../../service_broker/fetch';
import * as actionType from '../../store/state/actions';
import { getKeyAndFlowName, processHeaders } from '../../store/state/epics/util';
import HeaderProcessor from '../../service_broker/header-processor';
import { isAssignmentInCreateStage } from '../../utils/case-utils';
import Constants from '../../constants';
import EnvironmentInfo from '../../environment-info';

import { getViewType, addRemoteHeadersIfRequired } from './helper';

const OPEN_ASSIGNMENT_DXAPI_LOG_ERROR_MESSAGE = 'Error occurred during ajax call at openAssignment Epic : ';

const fetchOpenAssignmentInformation = ({
  restUrl,
  reqType,
  requestHeaders,
  debugInfo,
  context,
  isGetNextWork,
  doNotMergeMessagesForStatusCode
}: {
  restUrl: string;
  reqType?: string;
  requestHeaders: { [key: string]: any };
  debugInfo: { [key: string]: any };
  context: string;
  isGetNextWork: string;
  doNotMergeMessagesForStatusCode: string;
}) => {
  return fetch(
    restUrl,
    {
      method: reqType,
      headers: {
        'if-match': requestHeaders && requestHeaders.etag,
        ...requestHeaders,
        ...(debugInfo && { ...HeaderProcessor.addDebugHeaders(debugInfo) })
      }
    },
    context,
    {
      doNotMergeHttpMessagesForStatusCode: isGetNextWork ? doNotMergeMessagesForStatusCode : ''
    }
  );
};

export const executeOpenAssignmentRequest = (
  actionInPayload: {
    pageName?: any;
    isTraditionalUICase?: any;
    headers?: any;
    context?: any;
    assignmentID?: any;
    isGetNextWork?: any;
    debugInfo?: any;
    isOpenStage?: any;
    stageID?: any;
    caseID?: any;
    jsActionQueryParams?: any;
    isLastStepInCreateStage?: any;
  },
  options: { removeActionPayload: any }
) => {
  const viewType = getViewType(actionInPayload);
  const {
    headers,
    context,
    assignmentID,
    isGetNextWork,
    debugInfo,
    isOpenStage,
    stageID,
    caseID,
    jsActionQueryParams,
    isLastStepInCreateStage
  } = actionInPayload;
  const doNotMergeMessagesForStatusCode = '404';
  const { restApi, reqType } = isGetNextWork
    ? resolveRestApi(actionType.ACTION_GETNEXTWORK, {
        viewType
      })
    : isOpenStage
      ? resolveRestApi(actionType.ACTION_OPENSTAGE, {
          caseID,
          stageID
        })
      : resolveRestApi(actionType.ACTION_OPENASSIGNMENT, {
          assignmentID,
          viewType
        });
  const { removeActionPayload } = options;
  const defaultCasePage = EnvironmentInfo.getDefaultCasePage();
  let restUrl = '';
  let pageName = actionInPayload.pageName;
  if (actionInPayload.isTraditionalUICase) {
    pageName = 'pyDetails';
  }

  if (defaultCasePage) {
    restUrl = `${restApi}?viewType=${viewType}&pageName=${defaultCasePage}`;
  } else if (pageName) {
    restUrl = `${restApi}?viewType=${viewType}&pageName=${pageName}`;
  } else {
    restUrl = `${restApi}?viewType=${viewType}`;
  }

  if (jsActionQueryParams) {
    Object.keys(jsActionQueryParams).forEach((key) => {
      restUrl += `&${key}=${jsActionQueryParams[key]}`;
    });
  }
  const requestHeaders = addRemoteHeadersIfRequired(actionInPayload);

  return from(
    fetchOpenAssignmentInformation({
      restUrl,
      reqType,
      requestHeaders,
      debugInfo,
      context,
      isGetNextWork,
      doNotMergeMessagesForStatusCode
    })
  ).pipe(
    map((response) => {
      const isCoexistenceUIKITApp = EnvironmentInfo.isCoexistenceAppOfType(Constants.APP_TYPE.UIKIT);
      if (isCoexistenceUIKITApp && !isLastStepInCreateStage) {
        const isInCreateStage = isAssignmentInCreateStage(response.data.data.caseInfo);
        if (!isInCreateStage) {
          restUrl = restUrl.replaceAll(pageName, 'pyDetails');
          EnvironmentInfo.setDefaultCasePage('pyDetails');
          return {
            isReloadWithDifferentViewTypeRequired: true,
            restUrl
          };
        }
        return response;
      }
      return response;
    }),
    mergeMap((response: any) => {
      if (response?.isReloadWithDifferentViewTypeRequired) {
        return fetchOpenAssignmentInformation({
          restUrl,
          reqType,
          requestHeaders,
          debugInfo,
          context,
          isGetNextWork,
          doNotMergeMessagesForStatusCode
        });
      }
      return of(response);
    }),
    map((response) => {
      const { key, flowName } = getKeyAndFlowName(assignmentID);
      return {
        response: { ...response.data, key, flowName },
        actionInPayload,
        viewType,
        removeActionPayload,
        headers: processHeaders({ ...headers, ...response.headers })
      };
    }),
    logAndCatchError(OPEN_ASSIGNMENT_DXAPI_LOG_ERROR_MESSAGE, context)
  );
};

export default executeOpenAssignmentRequest;
