import ContextProcessor from '../../../context/context-processor';

import Parser from './parser';

const LITERAL = 'literal';
const IDENTIFIER = 'identifier';

const andGrammar = {
  operatorType: 'binaryOperator',
  precedence: 1,
  evaluate: (left: string, right: string) => left && right
};

const orGrammar = {
  operatorType: 'binaryOperator',
  precedence: 1,
  evaluate: (left: string, right: string) => left || right
};

/**
 * A map of all expression elements to their properties. Note that
 * changes here may require changes in the Lexer and Parser.
 */
export const supportedGrammar = {
  '>': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => left > right
  },
  '>=': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => left >= right
  },
  '<': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => left < right
  },
  '<=': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => left <= right
  },
  '==': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return left === right;
    }
  },
  '!=': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return left !== right;
    }
  },
  '!': {
    operatorType: 'unaryOperator',
    precedence: 999,
    evaluate: (right: any) => {
      let { value } = right;
      if (value === 'true' || value === 'false') {
        value = value === 'true';
      }
      return !value;
    }
  },
  '(': { operatorType: 'openParenthesis' },
  ')': { operatorType: 'closeParenthesis' },
  '&&': andGrammar,
  AND: andGrammar,
  and: andGrammar,
  '||': orGrammar,
  OR: orGrammar,
  or: orGrammar,
  CONTAINS: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return left.includes(right);
    }
  },
  NOT_CONTAINS: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return !left.includes(right);
    }
  },
  STARTS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return left.startsWith(right);
    }
  },
  NOT_STARTS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return !left.startsWith(right);
    }
  },
  ENDS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return left.endsWith(right);
    }
  },
  NOT_ENDS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      return !left.endsWith(right);
    }
  },
  IS_NULL: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (value: string) => {
      return !value;
    }
  },
  IS_NOT_NULL: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (value: string) => {
      return !!value;
    }
  },
  IS_IN_LIST: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      const list = right.split(',');
      return list.includes(left);
    }
  },
  IS_NOT_IN_LIST: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left: string, right: string) => {
      const list = right.split(',');
      return !list.includes(left);
    }
  }
};

/**
 * Expression handlers. Handles the different types of expression.
 */
const expressionHandlers: { [key: string]: any } = {};
/**
 * [BinaryExpression]
 * Description : Evaluates a binary expression.
 * @param ast     Abstract syntax tree
 * @returns true if the expression evaluates to true else false.
 */
expressionHandlers.BinaryExpression = function BinaryExpression(ast: any): boolean {
  const result = [];
  let leftType = '';
  result[0] = this.evaluateAST(ast.left);
  if (ast.left.operationType === IDENTIFIER && this._context) {
    leftType = this._context.getFieldMetadata(ast.left.value.substring(1))?.type ?? '';
  }
  result[1] = ast.right ? this.evaluateAST(ast.right, leftType) : '';
  return this._grammar[ast.operator].evaluate(result[0], result[1]);
};

/**
 * [identifier]
 * Description : Returns the identifier value from the context.
 * @param ast     Abstract syntax tree
 * @returns identifier value from the context
 */
expressionHandlers.identifier = function identifier(ast: any): any {
  if (this._localDataObject) {
    return this._localDataObject[ast.value.substring(1)] ?? '';
  }
  if (this._contextTreeNodeContext) {
    return ContextProcessor.getLitePropertyValue(this._contextTreeNodeContext, ast.value);
  }
  return this._context.getValue(ast.value) ?? '';
};

/**
 * [literal]
 * Description : Returns the literal value.
 * @param ast     Abstract syntax tree
 * @returns Literal value.
 */
expressionHandlers.literal = function literal(ast: any, leftType?: string): boolean | string | number {
  const hasleftType = leftType === undefined || leftType === '' || leftType === 'True-False';
  if (hasleftType && (ast.value === 'true' || ast.value === 'false')) {
    return ast.value === 'true';
  }
  return ast.value;
};

/**
 * [UnaryExpression]
 * Description : Evaluates a unary expression.
 * @param ast     Abstract syntax tree
 * @returns true if the expression evaluates to true else false.
 */
expressionHandlers.UnaryExpression = function UnaryExpression(ast: any): boolean {
  return this._grammar[ast.operator].evaluate(ast.right);
};

/**
 * Handlers for all the types of operators that are supported.
 */
const OperatorHandlers: { [key: string]: any } = {};
/**
 * [unaryOperator]
 * Description : Handles the unary operator.
 * @param token       Current token
 */
OperatorHandlers.unaryOperator = function unaryOperator(token: any) {
  this.setPositionInAST({
    operationType: 'UnaryExpression',
    operator: token.value
  });
};

/**
 * [binaryOperator]
 * Description : Handles the binary operator token.
 * @param token       Current token
 */
OperatorHandlers.binaryOperator = function binaryOperator(token: any) {
  const { precedence } = this._grammar[token.value];
  let parent = this._position._parent;
  while (parent && parent.operator && this._grammar[parent.operator].precedence >= precedence) {
    this._position = parent;
    parent = parent._parent;
  }
  const node = {
    operationType: 'BinaryExpression',
    operator: token.value,
    left: this._position
  };

  Parser._setParent(this._position, node);
  this._position = parent;
  this.setPositionInAST(node);
};

/**
 * [identifier]
 * Description : Handles the identifier token.
 * @param token       Current token
 */
OperatorHandlers.identifier = function identifier(token: any) {
  const node = {
    operationType: IDENTIFIER,
    value: token.value
  };
  this.setPositionInAST(node);
};

/**
 * [literal]
 * Description : Handles the literal token.
 * @param token       Current token
 */
OperatorHandlers.literal = function literal(token: any) {
  this.setPositionInAST({
    operationType: LITERAL,
    value: token.value
  });
};

/**
 * [subExpression]
 * Description : Handles the subExpression token.
 * @param ast       Abstract syntax tree
 */
OperatorHandlers.subExpression = function subExpression(ast: any) {
  this.setPositionInAST(ast);
};

/**
 * States in the evaluation of an expression.
 */
export const states: { [key: string]: any } = {
  expectOperand: {
    operatorTypes: {
      literal: { toState: 'expectBinOp' },
      identifier: { toState: IDENTIFIER },
      unaryOperator: {},
      openParenthesis: { toState: 'subExpression' }
    }
  },
  expectBinOp: {
    operatorTypes: {
      binaryOperator: { toState: 'expectOperand' }
    }
  },
  identifier: {
    operatorTypes: {
      binaryOperator: { toState: 'expectOperand' }
    }
  },
  subExpression: {
    subHandler: OperatorHandlers.subExpression,
    endStates: {
      closeParenthesis: 'expectBinOp'
    }
  }
};

export { OperatorHandlers, expressionHandlers };
