import * as RulesStore from '../rules/rules-store';
import AnnotationProcessor from '../annotation/processor';
import ContextProcessor from '../context/context-processor';
import * as CaseUtils from '../utils/case-utils';
import { ActionManagerInstance } from '../actions/manager';
import Utils from '../utils/utils';
import ContextTreeManager from '../contextree/treemanager';
import type { CalculatedField } from '../types';
import type { C11nEnv } from '../interpreter/c11n-env';

import type { ReqConfig } from './types';
import { refreshData, fillFormWithAI } from './action-creator';

// Keeps track of the views with refresh targets.
export const refreshTargetViewsList: { [key: string]: any } = {};

/**
 * Description : Removes stale container item from refreshList
 * @param context container name
 */
export const removeViewsFromTargetList = (context: string) => {
  if (context && refreshTargetViewsList[context]) {
    delete refreshTargetViewsList[context];
  }
};

/**
 * [dispatchRefreshData]
 * Description : Dispatches the REFRESH_DATA action
 * @param ID              CaseID or AssignmentID
 * @param actionID        Action ID either a casewide action or assignment action
 * @param context         Data context
 * @param caseWideAction  Whether the action is a case wide action or not.
 * @param options         options object for actions
 */
export const dispatchRefreshData = (
  ID: string,
  actionID: string,
  context: string | null,
  caseWideAction: boolean,
  options: any
) => {
  if (!(ID && context && actionID)) {
    return;
  }

  const actionMgrID = options.actionMgrID || ActionManagerInstance.getNextActionMgrId();

  // payload to the REFRESH_DATA action
  const payload = {
    ID,
    actionID,
    context,
    caseWideAction,
    options: {
      ...options,
      actionMgrID
    }
  };
  if (options.isFillFormWithAI) {
    dispatch(fillFormWithAI(payload));
  } else {
    // Dispatching REFRESH_DATA
    dispatch(refreshData(payload));
  }
};

/**
 * [dispatchCaseViewRefresh]
 * Description : Dispatches the REFRESH_DATA action
 * @param ID              CaseID or AssignmentID
 * @param viewID          View ID
 * @param context         Data context
 * @param pageReference   page reference
 * @param options         Options object for action
 */
export const dispatchCaseViewRefresh = (
  ID: string,
  viewID: string,
  context: string,
  pageReference: string,
  options: any
) => {
  if (!(ID && context && viewID)) {
    return;
  }

  if (pageReference) {
    pageReference = Utils.convertPageRefToClipboardPageNotation(pageReference);
  }

  // payload to the REFRESH_DATA action
  const payload = {
    ID,
    viewID,
    context,
    options,
    caseViewAction: true,
    interestPage: pageReference
  };
  // Dispatching REFRESH_DATA
  dispatch(refreshData(payload));
};

/**
 * [recordViewForRefresh]
 * Description : Pushes the view to a list. This list holds all the views which have refresh targets.
 * @param viewName        view name from which the refresh is triggered
 * @param context         Data context
 * @param pageReference   Page reference inside the content
 */
export const recordViewForRefresh = (viewName: string | undefined, context: string, pageReference: string) => {
  if (!refreshTargetViewsList[context]) {
    refreshTargetViewsList[context] = {};
  }
  if (viewName && !refreshTargetViewsList[context][viewName]) {
    refreshTargetViewsList[context][viewName] = { pageReference };
  }
};

/**
 * [shouldViewRefresh]
 * Description : Returns true if context has any views present in it,else returns false.
 *               Refresh will happen for all the views present in that context.
 * @param context     Data context
 * @returns boolean
 */
export const shouldViewRefresh = (context: string) => {
  return (
    refreshTargetViewsList[context] &&
    Object.keys(refreshTargetViewsList[context]).length > 0 &&
    CaseUtils.isPerform(context)
  );
};

/**
 * This returns the leaf page name and property name of the embedded property
 * @param property with annotation
 * @returns containing pageName and propertyName
 * @example if we pass '@P .Address.Street' it will return { pageName : ".Address", propertyName : "Street"}
 */
const getPropertyReferenceInfo = (property: string) => {
  const pageName = property.substring(0, property.lastIndexOf('.'));
  const propertyName = property.substring(property.lastIndexOf('.') + 1);

  return { pageName, propertyName };
};

/**
 *This takes the property reference value as input and returns the isDeclarativeTarget value from property metadata
 * @param component C11nEnv object
 * @param paramValue data source parameter value
 * @returns isDeclarativeTarget
 *
 */
const extractDeclarativeTargetFromParam = (component: C11nEnv, paramValue: string): boolean => {
  const propertyReferenceObj = getPropertyReferenceInfo(paramValue.substring(paramValue.lastIndexOf(' ') + 1));
  const paramMetadata = RulesStore.getPropertyMetadata(
    propertyReferenceObj.propertyName,
    component.getValue(constants.CLASS_ID, propertyReferenceObj.pageName)
  );
  // @ts-expect-error
  return !!paramMetadata?.isDeclarativeTarget;
};

/**
 * This function takes c11Env object and datasource parameters and returns whether parameters are
 * declarative expression or not
 * @param component C11nEnv object
 * @param parameters datasource parameters can be object/array
 * @returns isDeclarativeTarget boolean value
 */
export const checkDataSourceParamForDeclarativeTargets = (
  component: C11nEnv,
  parameters: { [key: string]: string }[] | { [key: string]: string }
) => {
  let viewShouldRefresh = false;
  if (Array.isArray(parameters)) {
    const checkForDeclarativeTargets = (param: any) => {
      const paramValue = param.value;
      if (paramValue.startsWith('@P')) {
        viewShouldRefresh = extractDeclarativeTargetFromParam(component, paramValue);
      }
      return viewShouldRefresh;
    };
    parameters.some(checkForDeclarativeTargets);
  } else if (typeof parameters === 'object') {
    for (const key in parameters) {
      if (parameters[key].startsWith('@P')) {
        viewShouldRefresh = extractDeclarativeTargetFromParam(component, parameters[key]);
        if (viewShouldRefresh) break;
      }
    }
  }
  return viewShouldRefresh;
};

/**
 * [inspectComponentForRefreshTargets]
 * Description : Inspects the component, checks if there are any refresh targets in it.
 * If any refresh targets, calls a method which tracks the view for further processing.
 * @param component   C11nEnv object
 */
export const inspectComponentForRefreshTargets = (component: C11nEnv) => {
  let viewShouldRefresh = false;
  // view data should be refreshed if any component in the view has a when authored on it.
  if (component.isWhenExist()) {
    viewShouldRefresh = true;
  } else {
    const prop = component.getRawMetadata()?.config?.value;
    if (prop) {
      const propMetadata = RulesStore.getPropertyMetadata(
        AnnotationProcessor.getLeafPropertyName(prop),
        ContextProcessor.resolveClassID(prop, component.getPageReference(), component.getContextName())
      );
      // view data should be refreshed if any component in the view is a declare target.
      // @ts-expect-error
      viewShouldRefresh = !!propMetadata?.isDeclarativeTarget;

      // @ts-expect-error
      if (!viewShouldRefresh && propMetadata?.datasource?.parameters) {
        // @ts-expect-error
        viewShouldRefresh = checkDataSourceParamForDeclarativeTargets(component, propMetadata?.datasource?.parameters);
      }
      // picklist of DECISION type source
      if (!viewShouldRefresh) {
        // @ts-expect-error
        viewShouldRefresh = propMetadata?.datasource?.tableType === constants.DECISION;
      }
    }
  }
  // View should be refreshed only if any of its components has when authored or is a declare target
  if (viewShouldRefresh) {
    recordViewForRefresh(component.getCurrentView(), component.getContextName(), component.getPageReference());
  }
};

export const refreshAssignment = (context: string | null, pageReference: string, options: any) => {
  const caseWideAction = CaseUtils.isCaseWideAction(context, pageReference);
  // Get actionID
  const actionID = ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.ACTIVE_ACTION_ID, pageReference, context)
    ? ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.ACTIVE_ACTION_ID, pageReference, context)
    : ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.ASSIGNMENTACTION_ID, pageReference, context);
  let ID: string;
  if (caseWideAction) {
    ID = ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.CASE_INFO_ID, pageReference, context);
  } else if (ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.ASSIGNMENT_ID, pageReference, context)) {
    ID = ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.ASSIGNMENT_ID, pageReference, context);
  } else {
    ID = ContextProcessor.getPropertyValue(publicConstants.CONTEXT_DATA_ASSIGNMENTID, pageReference, context);
  }
  dispatchRefreshData(ID, actionID, context, caseWideAction, options);
};

/**
 * [refreshOnPropertyChange]
 * Description : This function gets the action type, action/assignment ID and gives a call to dispatch REFRESH_DATA
 * @param payload
 *          payload.context - Data context
 *          payload.pageReference - Page reference in the content
 */
export const refreshOnPropertyChange = (payload: any) => {
  const { context, pageReference } = payload;

  const viewsTobeRefreshed = shouldViewRefresh(context);
  // Dispatch action to refresh the data only if any views are tracked for refresh.
  if (!viewsTobeRefreshed) {
    return;
  }
  const isMultiRecordData = ContextProcessor.getPropertyValue(
    constants.CONTEXT_DATA_MODAL_VIEW.IS_MULTI_RECORD_DATA,
    pageReference,
    context
  );

  if (isMultiRecordData) {
    const caseID = ContextProcessor.getPropertyValue(publicConstants.CASE_INFO.CASE_INFO_ID, pageReference, context);
    const viewID = ContextProcessor.getPropertyValue(
      constants.CONTEXT_DATA_MODAL_VIEW.VIEW_NAME,
      pageReference,
      context
    );
    const interestPage = ContextProcessor.getPropertyValue(
      constants.CONTEXT_DATA_MODAL_VIEW.INTERESTPAGE,
      pageReference,
      context
    );
    dispatchCaseViewRefresh(caseID, viewID, context, interestPage, {});
  } else {
    refreshAssignment(context, pageReference, {});
  }
};

export const appendQueryParamsForRefreshAPI = (payload: any, restApi?: string) => {
  const { requestType } = payload;
  if (payload?.options?.refreshFor && requestType === 'refresh') {
    restApi += `?refreshFor=${payload?.options?.refreshFor}`;
  }
  if (payload?.options?.isFillFormWithAI && requestType === 'refresh') {
    restApi += '?fillFormWithAI=true';
  }
  return restApi;
};

/**
 * This API will return the Fields which are configured with declarative target in the specified context.
 * @param contextName - name of the context.
 * @returns {CalculatedField[]} - returns an Array of objects.
 */
const getDeclarativeTargetFields = (contextName: string): CalculatedField[] => {
  const calculatedFields: CalculatedField[] = [];
  const declarativeFields = ContextTreeManager.getDeclarativeTargets(contextName);
  declarativeFields.forEach(({ name, pageReference }) => {
    const pageReferenceSplitArray = pageReference.split('.');
    pageReferenceSplitArray.shift();
    let context = pageReferenceSplitArray.join('.');
    if (name && context) {
      context = Utils.convertPageRefToClipboardPageNotation(context);
      calculatedFields.push({
        name,
        context
      });
    }
  });
  return calculatedFields;
};

/**
 * This API will return the Fields and Views which are configured with WHEN condition in the specified context.
 * @param contextName
 * @returns {CalculatedField[]} - returns an Array of objects.
 */
const getWhens = (contextName: string): CalculatedField[] => {
  const calculatedFields: CalculatedField[] = [];
  const whens = ContextTreeManager.getWhens(contextName);
  whens.forEach(({ name, type, pageReference }) => {
    const pageReferenceSplitArray = pageReference.split('.');
    pageReferenceSplitArray.shift();
    let context = pageReferenceSplitArray.join('.');
    if (name && context) {
      context = Utils.convertPageRefToClipboardPageNotation(context);
      calculatedFields.push({
        name,
        context,
        type
      });
    }
  });
  return calculatedFields;
};

/**
 * This API will returns fields which configured with declarative target, field and views which are
 * configured with when condition in the specified context.
 * @param contextName - name of the context.
 * @returns
 */
export const getItemsToRecalculate = (contextName: string) => {
  const fieldsWithDeclarativeTargets: CalculatedField[] = getDeclarativeTargetFields(contextName);
  const whens: CalculatedField[] = getWhens(contextName);
  return { fieldsWithDeclarativeTargets, whens };
};

export const shouldCancelRequest = (existingRequest: ReqConfig, incomingRequest: ReqConfig): boolean | string => {
  const isExistingRefreshFor = existingRequest.url?.includes('/refresh?refreshFor=');
  const isIncomingRefreshFor = incomingRequest.url?.includes('/refresh?refreshFor=');
  const isExistingRefresh = existingRequest.url?.endsWith('/refresh') && !isExistingRefreshFor;
  const isIncomingRefresh = incomingRequest.url?.endsWith('/refresh') && !isIncomingRefreshFor;

  if (isExistingRefresh && isIncomingRefresh) {
    return existingRequest.url === incomingRequest.url;
  }

  if (isExistingRefresh && isIncomingRefreshFor) {
    return true;
  }

  if (isExistingRefreshFor) {
    if (isIncomingRefresh) {
      return 'CANCEL_REFRESH';
    }
    /* istanbul ignore else */
    if (isIncomingRefreshFor) {
      return false;
    }
  }

  return false;
};
