import * as StateManager from '../../store/state/state-manager';
import Utils from '../../utils/utils';
import { getNormalizedPropertyName } from '../../utils/form-handler';
import { listAction } from '../../store/state/actions/action-creators';
import type { Action } from '../../store/state/state-manager';

import Insert from './states/insert';
import Update from './states/update';
import Delete from './states/delete';
import DeleteAll from './states/delete-all';
import Move from './states/move';
import ReplacePage from './states/replace-page';
import DeletePage from './states/delete-page';
import StateNode from './state-node';
import type { StateNodeInstruction } from './state-node';

class StateMachine {
  public contextName: string;
  public target: string;
  public pageReference: string;
  public changeList: any;
  public targetPageReference: string;
  current: any;
  root: any;
  visibility: boolean;
  children: any[];
  constructor(target: string, pageReference: string, contextName: string) {
    this.target = target;
    this.pageReference = pageReference;
    this.contextName = contextName;
    this.root = null;
    this.current = null;
    this.changeList = {};
    this.targetPageReference = pageReference + target;
    this.children = []; // state machines of children
    this.visibility = true;
  }

  addChild(stateMachine: StateMachine) {
    if (this.current === null) {
      this.insertNode({});
    }
    this.current.childrenStateMachines.push(stateMachine);
  }

  insertNode(instruction: StateNodeInstruction) {
    const node = new StateNode(instruction);
    if (this.root === null) {
      this.root = node;
    }
    if (this.current !== null) {
      node.previous = this.current;
      this.current.next = node;
    }
    this.current = node;
  }

  buildChildSumbitPageInstructions(stateNode: { childrenStateMachines: any[] }, isOnlyChanged: boolean) {
    let instructions: any[] = [];
    stateNode.childrenStateMachines.forEach(
      (childStateMachine: { visibility: any; buildSubmitPageInstructions: (arg0: any) => any }) => {
        /* istanbul ignore else */
        if (childStateMachine.visibility) {
          instructions = instructions.concat(childStateMachine.buildSubmitPageInstructions(isOnlyChanged));
        }
      }
    );
    return instructions;
  }

  // for form submit
  buildSubmitPageInstructions(isOnlyChanged = false) {
    let stateNode = this.root;
    let instructions: any[] = [];
    while (stateNode) {
      if (stateNode.instruction.instruction && !(isOnlyChanged && stateNode.automatic)) {
        instructions.push(stateNode.instruction);
      }
      instructions = [...instructions, ...this.buildChildSumbitPageInstructions(stateNode, isOnlyChanged)];
      stateNode = stateNode.next;
    }
    return instructions;
  }

  // push default page instruction
  initPageInstructions(c11nEnv: { getValue: (arg0: string) => any[] }, propertyNames: any[], uniqueField: string) {
    const rows = c11nEnv.getValue(this.target) || [];
    if (propertyNames) {
      rows.forEach((row: { [x: string]: any }, index: number) => {
        propertyNames.forEach((property: string) => {
          const normalizedProperty = getNormalizedPropertyName(property);
          const uuidPayload: { [key: string]: any } = {};
          let skipStateUpdate = true;
          if (uniqueField) {
            uuidPayload[getNormalizedPropertyName(uniqueField)] = crypto.randomUUID();
            skipStateUpdate = false;
          }
          const payload = {
            [normalizedProperty]: Utils.getIn(row, normalizedProperty),
            ...uuidPayload
          };
          this.update(payload, index + 1, skipStateUpdate, { isDefault: true });
        });
      });
      return;
    }
    /* const editableFields = FormUtils.getEditableFields(this.contextName);

    // [{
    //  label: "DateTimeField"
    //  name: "caseInfo.content.EmbeddedFieldNew[0].DateTimeField"
    //  type: "datetime",
    //  fieldC11nEnv
    // }]

    editableFields
      .filter((fileObject) => fileObject.fieldC11nEnv.getReferenceList() === c11nEnv.getStateProps().referenceList)
      .forEach((fieldObject) => {
        const { fieldC11nEnv } = fieldObject;
        const fieldProperty = fieldC11nEnv.getStateProps()?.value;
        if (fieldC11nEnv.isInsideList() && fieldProperty) {
          fieldC11nEnv
            .getListActions()
            .updateProperty(fieldProperty, fieldC11nEnv.getValue(fieldProperty), { isDefault: true });
          if (uniqueField) {
            fieldC11nEnv.getListActions().updateProperty(uniqueField, uuidV4(), {
              isDefault: true,
              skipStateUpdate: false,
              skipPageinstruction: true
            });
          }
        }
      }); */

    rows.forEach((row: { [x: string]: any }, index: number) => {
      /* istanbul ignore else */
      if (uniqueField && !row[getNormalizedPropertyName(uniqueField)]) {
        const payload: Action['payload'] = {};
        payload[getNormalizedPropertyName(uniqueField)] = crypto.randomUUID();
        this.update(payload, index + 1, false, { isDefault: true });
      }
    });
  }

  // for refersh compare
  getChangeList() {
    return this.changeList;
  }

  initChangeList(index: number) {
    this.changeList[this.targetPageReference] = this.changeList[this.targetPageReference] || [];
    this.changeList[this.targetPageReference][index] = this.changeList[this.targetPageReference][index] || {};
  }

  // create page instructions node
  update(
    payload: object,
    index: number,
    skipStateUpdate: boolean,
    options: { skipPageinstruction?: boolean; isDefault?: boolean; isArrayDeepMerge?: boolean }
  ) {
    const { isDefault = false, skipPageinstruction } = options;
    const instruction = Update.execute(
      this.contextName,
      this.target,
      index,
      payload,
      this.pageReference,
      skipStateUpdate,
      isDefault
    );
    if (skipPageinstruction) {
      return;
    }
    this.initChangeList(index - 1);
    if (!isDefault) {
      const previousChange = this.changeList?.[this.targetPageReference]?.[index - 1].content;
      this.changeList[this.targetPageReference][index - 1] = StateManager.mergeDeep(
        Utils.deepClone(previousChange),
        payload,
        options
      );
    }
    if (
      this.current?.automatic === isDefault &&
      this.current?.instruction?.listIndex === index &&
      this.current?.instruction.instruction === 'UPDATE'
    ) {
      const previousContent = this.current.instruction.content;
      this.current.instruction.content = StateManager.mergeDeep(
        Utils.deepClone(previousContent),
        Utils.unflatten(payload),
        options
      );
      return;
    }
    this.insertNode(instruction);
    if (isDefault) {
      this.current.automatic = true;
    }
  }

  delete(index: number, skipStateUpdate: boolean | undefined) {
    const instruction = Delete.execute(this.target, index, this.pageReference);
    this.changeList[this.targetPageReference]?.splice(index - 1, 1);
    this.insertNode(instruction);
    if (!skipStateUpdate) {
      StateManager.dispatch(
        listAction({
          instruction: 'DELETE',
          listIndex: index - 1,
          context: this.contextName,
          referenceList: `${this.pageReference}${this.target}`
        })
      );
    }
  }

  insert(
    payload: any,
    index: number,
    skipStateUpdate: boolean | undefined,
    options: { isDefault?: boolean; isArrayDeepMerge?: boolean }
  ) {
    /* istanbul ignore next */
    const {
      instruction,
      actionPayload = { content: {} },
      displayPropertPayload = {}
    } = Insert.execute(this.target, index, payload, this.pageReference);
    const { isDefault = false } = options;
    this.initChangeList(index - 1);
    if (!isDefault) {
      const previousChange = this.changeList?.[this.targetPageReference]?.[index - 1].content;
      this.changeList[this.targetPageReference][index - 1] = StateManager.mergeDeep(
        Utils.deepClone(previousChange),
        payload,
        options
      );
    }
    if (this.root && !this.root.instruction?.instruction && this.current && !this.current.instruction?.instruction) {
      this.root.instruction = instruction;
    } else {
      this.insertNode(instruction);
    }
    if (!skipStateUpdate) {
      actionPayload.content = { ...actionPayload.content, ...displayPropertPayload };
      StateManager.dispatch(
        listAction({
          ...actionPayload,
          listIndex: index - 1,
          context: this.contextName,
          referenceList: `${this.pageReference}${this.target}`
        })
      );
    }
    if (isDefault) {
      this.current.automatic = true;
    }
  }

  replacePage(payload: object) {
    const instruction = ReplacePage.execute(this.contextName, this.target, payload, this.pageReference);
    this.insertNode(instruction);
  }

  deletePage() {
    const instruction = DeletePage.execute(this.target, this.pageReference);
    this.insertNode(instruction);
  }

  move(fromIndex: number, toIndex: number) {
    const value = this.changeList[this.targetPageReference]?.splice(fromIndex - 1, 1);
    this.changeList[this.targetPageReference]?.splice(toIndex - 1, 0, value[0]);
    const instruction = Move.execute(this.target, fromIndex, toIndex, this.pageReference);
    this.insertNode(instruction);
    StateManager.dispatch(
      listAction({
        listIndex: fromIndex - 1,
        listMoveToIndex: toIndex - 1,
        context: this.contextName,
        referenceList: `${this.pageReference}${this.target}`,
        instruction: 'MOVE'
      })
    );
  }

  deleteAll(skipStateUpdate: boolean) {
    const instruction = DeleteAll.getDeleteAllInstruction(this.pageReference, this.target);
    this.changeList[this.targetPageReference] = [];
    this.insertNode(instruction);
    DeleteAll.execute(this.contextName, this.target, this.pageReference, skipStateUpdate);
  }

  updatePage(payload: any) {
    const previousContent = this.current.instruction.content;
    this.current.instruction.content = StateManager.mergeDeep(Utils.deepClone(previousContent), payload);
  }
}

export default StateMachine;
