import { C11nEnv } from '../interpreter/c11n-env';
import { localeUtils } from '../locale/locale-utils';
import { invokeRestApi, getCancelTokenSource, isRequestCanceled } from '../rest-client';
import { updateViewResources } from '../utils/view-resources';

const POST_FEED_MESSAGES = 'postFeedMessages';
const DELETE_FEED_MESSAGE = 'deleteFeedMessage';
const GET_FEED_MESSAGES = 'getFeedMessages';
const LIKE_FEED_MESSAGES = 'likeFeedMessages';
const UNLIKE_FEED_MESSAGES = 'unlikeFeedMessages';
const GET_MENTION_SUGGESTIONS = 'getMentionSuggestions';
const GET_TAG_SUGGESTIONS = 'getTagSuggestions';
const GET_FEED_MESSAGE_LIKES = 'getFeedMessageLikes';
const EDIT_FEED_MESSAGE = 'editFeedMessage';
const GET_MENTION_TYPES = 'getMentionTypes';

const headers = { 'Content-Type': 'application/json' };

/**
 * @description Use the APIs in the FeedUtils class to handle the feeds of a case.
 */
class FeedUtils {
  public mentionsTagsCancelTokenSource: any;
  public mentions: any;

  constructor() {
    this.mentionsTagsCancelTokenSource = [];
    this.mentions = [];
  }

  /**
   * Obtains the feeds for a given context.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the feed of a context whose feedID is pyDashboardFeed.</caption>
   * PCore.getFeedUtils().getFeeds('DATA-PORTAL $EngPMF', 'pyDashboardFeed','class',[{id: 'All', label: 'All', on: false, disabled: false}],[], getPConnect(), true)
   * .then(feedResponse => {
   *    // feedResponse array
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API obtains the feed of a context whose feedID is pyCaseFeed.</caption>
   * PCore.getFeedUtils().getFeeds('PEGAPROJMGMT-WORK TASK-100', 'pyCaseFeed','class',[{id: 'All', label: 'All', on: false, disabled: false}],[], getPConnect(), true)
   * .then(feedResponse => {
   *    // feedResponse array
   * }).catch(err => {
   *   // errors
   * });
   *
   * @param pulseContext                     The name of the application context or case context for which the feed must be fetched.
   * @param feedID                           The ID of the feed that must be fetched.
   *                                                         FeedID of the Pulse widget, when configured on the Home page, is known as "pyDashboardFeed"
   *                                                         and when used within a case view, it is known as "pyCaseFeed."
   * @param feedClass                        The class associated with the feed to be fetched.
   * @param feedFilters                      The list of conditions through which the feed to be fetched is filtered.
   *                                                         It is an array comprising objects, with each object containing four distinct properties.
   *                                                         id and label values for the filter options are available in pyDashboardFeed and pyCaseFeed rules.
   *                                                         Extra filter options if needed can be configured in the same rules.
   * @param     {string}    feedFilters.id                   The unique identifier for the feed source
   * @param     {string}    feedFilters.label                The title of the feed source
   * @param     {boolean}   feedFilters.disabled             This can be used to deactivate the filter (with a default value of false)
   * @param     {boolean}   feedFilters.on                   This provides information regarding whether the filter is currently selected.(with a default value of false i.e., not selected)
   * @param fetchFeedsCancelTokenSource      The list of API requests for fetching the feed.
   * @param c11nEnv                          The name of the context where the API is being called.
   * @param isLoadMore                       The flag that determines if the next set of feeds should be loaded.
   *                                                         The default value is false. Set isLoadMore to true if you want to load the next set of feeds.
   * @returns A Promise that resolves to an object.
   */
  getFeeds = (
    pulseContext: string,
    feedID: string,
    feedClass: string,
    feedFilters: [any],
    fetchFeedsCancelTokenSource: [any],
    c11nEnv: C11nEnv,
    isLoadMore: boolean
  ) => {
    const { getLocaleValue } = localeUtils;
    if (!isLoadMore) this.mentions = [];
    let filterBy = '';
    if (feedFilters && Array.isArray(feedFilters) && feedFilters?.length > 0) {
      feedFilters.forEach((feedFilter) => {
        /* istanbul ignore else */
        if (feedFilter.on) filterBy = `${filterBy}${feedFilter.id},`;
      });
      /* istanbul ignore if */
      if (filterBy === '') filterBy = 'ClearFilters';
    }
    let olderThan = '';
    if (isLoadMore) {
      olderThan = c11nEnv.getValue('pulse.pageLastEntry');
    }

    const queryPayload = {
      filterForContext: pulseContext,
      feedID,
      feedClass,
      filterBy,
      pageSize: 10,
      olderThan
    };

    for (let i = 0; i < fetchFeedsCancelTokenSource.length; i += 1) {
      fetchFeedsCancelTokenSource[i].cancel();
    }
    const newCancelTokenSource = getCancelTokenSource();
    fetchFeedsCancelTokenSource.push(newCancelTokenSource);
    return invokeRestApi(
      GET_FEED_MESSAGES,
      { queryPayload, cancelTokenSource: newCancelTokenSource },
      c11nEnv.getContextName()
    )
      .then(async (response: any) => {
        if (response.status === 200 && response.data) {
          fetchFeedsCancelTokenSource.pop();
          const respMessageIDs: any[] = [];
          const respMessages: { [key: string]: any } = {};
          let isCustomViewConfigured = false;

          /* istanbul ignore else */
          if (response?.data) {
            /* istanbul ignore else */
            if (response?.data?.data?.FeedList) {
              const feedItemList = response.data.data.FeedList?.pxResults;
              /* istanbul ignore else */
              if (Array.isArray(feedItemList) && feedItemList?.length > 0) {
                feedItemList.forEach((message, index) => {
                  message.ID = message.pzInsKey !== '' ? message.pzInsKey : `message${index}`;
                  respMessageIDs.push(message.ID);
                  respMessages[message.ID] = message;
                  /* istanbul ignore else */
                  if (message?.mentions && Array.isArray(message?.mentions) && message.mentions?.length > 0) {
                    this.mentions.push(message.mentions);
                  }
                  /* istanbul ignore else */
                  if (message?.pxResults && Array.isArray(message?.pxResults) && message.pxResults?.length > 0) {
                    message.pxResults.forEach((item: any) => {
                      /* istanbul ignore else */
                      if (item?.mentions && Array.isArray(item?.mentions) && item.mentions?.length > 0) {
                        this.mentions.push(item.mentions);
                      }
                    });
                  }
                  /* istanbul ignore else */
                  if (
                    !isCustomViewConfigured &&
                    message.pyMessageViewReference !== 'pzPostDetails' &&
                    message.pyMessageViewReference !== 'pzPrivatePostDetails'
                  )
                    isCustomViewConfigured = true;
                });
              }
            }
            /* istanbul ignore else */
            if (isCustomViewConfigured) {
              await updateViewResources({ uiResources: response.data.uiResources });
            }

            if (!feedFilters) {
              feedFilters = [{ id: 'All', label: getLocaleValue('All', 'Pulse'), on: true, disabled: false }];
              /* istanbul ignore else */
              if (response?.data?.data?.FeedFilters) {
                const feedFiltersList = response.data.data.FeedFilters?.pxResults;
                let allFilter = true;
                /* istanbul ignore else */
                if (feedFiltersList && Array.isArray(feedFiltersList) && feedFiltersList?.length > 0) {
                  feedFiltersList.forEach((feedFilter) => {
                    const feedFilterItem = {
                      id: feedFilter.pyFeedSourceReference,
                      label: getLocaleValue(feedFilter.pyLabel, 'Pulse'),
                      disabled: false,
                      on: feedFilter.pySelected === 'true'
                    };
                    /* istanbul ignore else */
                    if (!feedFilterItem.on) allFilter = false;
                    feedFilters.push(feedFilterItem);
                  });
                  feedFilters.find((feedItem) => feedItem.id === 'All').on = allFilter;
                }
              }
            }
          }

          if (isLoadMore && c11nEnv.getValue('pulse.messageIDs')) {
            let messageIDs: any = c11nEnv.getValue('pulse.messageIDs');
            let messages = c11nEnv.getValue('pulse.messages');
            messageIDs = [...messageIDs, ...respMessageIDs];
            messages = { ...messages, ...respMessages };
            Object.values(messages).forEach((item: any) => {
              /* istanbul ignore else */
              if (item?.mentions && Array.isArray(item?.mentions) && item.mentions?.length > 0) {
                this.mentions.push(item.mentions);
              }
              if (item?.pxResults && Array.isArray(item?.pxResults) && item.pxResults?.length > 0) {
                item.pxResults.forEach((ele: any) => {
                  /* istanbul ignore else */
                  if (ele?.mentions && Array.isArray(ele?.mentions) && ele.mentions?.length > 0) {
                    this.mentions.push(ele.mentions);
                  }
                });
              }
            });
            c11nEnv.updateState({
              pulse: {
                messages,
                messageIDs,
                nextPageFirstEntry: response?.data?.nextPageFirstEntry || '',
                pageLastEntry: response?.data?.pageLastEntry || '',
                hideAttachment: response?.data?.hideAttachment || false
              }
            });
          } else {
            c11nEnv.updateState({
              pulse: {
                messages: respMessages,
                messageIDs: respMessageIDs,
                nextPageFirstEntry: response?.data?.nextPageFirstEntry || '',
                pageLastEntry: response?.data?.pageLastEntry || '',
                hideAttachment: response?.data?.hideAttachment || false
              }
            });
          }
          // Removing duplicate mention objects from mentions array.
          const caseMentions =
            [...new Map(this.mentions.flat()?.map((item: any) => [item.mentionsID, item])).values()]
              ?.filter((item) => Object.prototype.hasOwnProperty.call(item, 'mentionsType'))
              ?.filter((item: any) => !item?.mentionsType?.toLowerCase().includes('user')) || [];
          return { feedFilters, caseMentions };
        }
        c11nEnv.reportError(`getFeeds call failed ${response.status}`, response.data);
        return '';
      })
      .catch((error: any) => {
        /* istanbul ignore else */
        if (!isRequestCanceled(error)) {
          c11nEnv.reportError(': Error occurred during ajax call at getFeeds API : ', error.response);
        }
      });
  };

  /**
   * Posts a message to the given context.
   * @function
   * @static
   * @example <caption>In this example, the API adds the post along with attachment to the given context.</caption>
   * PCore.getFeedUtils().postMessage('DATA-PORTAL $appName','test message', [{"type":"File","category":"File","fileName":"attachment.png","ID":"459c"}], false, getPConnect())
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @example <caption>In this example, the API adds the reply to the post along with attachment to the provided context.</caption>
   * PCore.getFeedUtils().postMessage('PEGASOCIAL M-214007','test reply message', [{"type":"File","category":"File","fileName":"attachment.png","ID":"459c"}], true, getPConnect())
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @param pulseContext    The name of the application context or case context for which the message must be posted.
   * @param message         The message that needs to be posted.
   * @param attachmentIDs   The metadata of the attachments that need to be posted along with the message.
   * @param isReply         The flag that determines if you want to post a message or a reply to a message.The default value is false. Set isReply to true if you want to post a reply to a message. Set isReply to false if you want to post a message.
   * @param c11nEnv         The name of the context where the API is being called.
   * @returns A Promise that resolves to an object.
   */
  postMessage = (
    pulseContext: string,
    message: string,
    // eslint-disable-next-line @typescript-eslint/default-param-last
    attachmentIDs: any[] = [],
    // eslint-disable-next-line @typescript-eslint/default-param-last
    isReply = false,
    c11nEnv: C11nEnv
  ) => {
    const reqBody = JSON.stringify({
      context: pulseContext,
      parentMessageId: isReply ? pulseContext : '',
      message,
      attachments: attachmentIDs
    });
    return invokeRestApi(POST_FEED_MESSAGES, { body: reqBody, headers }, c11nEnv.getContextName())
      .then((response: any) => {
        if ((response.status === 201 || response.status === 200) && response.data) {
          const messageData = response?.data;
          if (isReply) {
            messageData.parentMessageID = pulseContext;
            const { pxResults, caseMentions } = this.buildMessageObject(
              messageData,
              isReply,
              c11nEnv,
              attachmentIDs.length > 0
            );
            c11nEnv.updateState({ pulse: { messages: { [pulseContext]: { pxResults } } } });
            return caseMentions;
          }
          const { messages, messageIDs, caseMentions } = this.buildMessageObject(
            messageData,
            isReply,
            c11nEnv,
            attachmentIDs.length > 0
          );
          c11nEnv.updateState({ pulse: { messages, messageIDs } });
          return caseMentions;
        }
        c11nEnv.reportError(`postMessage call failed with status ${response.status}`, response);
        return '';
      })
      .catch((error: any) => {
        c11nEnv.reportError(': Error occurred during ajax call at postMessage API : ', error.response);
      });
  };

  /**
   * Modifies a message associated with the given context.
   * @function
   * @static
   * @example <caption>In this example, the API edits the post whose messageID is PEGASOCIAL M-100</caption>
   *  const payload = {
   *    messageID:'PEGASOCIAL M-100',
   *    parentMessageID:'PEGASOCIAL M-100',
   *    message:'test message',
   *    isReply:false,
   *    c11nEnv: getPConnect()
   *  }
   * PCore.getFeedUtils().editMessage(payload)
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @example <caption>In this example, the API edits the reply to the post whose messageID is PEGASOCIAL M-101 and the parentMessageID is PEGASOCIAL M-100</caption>
   *  const payload = {
   *    messageID:'PEGASOCIAL M-101',
   *    parentMessageID:'PEGASOCIAL M-100',
   *    message:'test reply message',
   *    isReply:true,
   *    c11nEnv: getPConnect()
   *  }
   * PCore.getFeedUtils().editMessage(payload)
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @param param                 The object that contains the required data to edit a message.
   * @param param.messageID       The unique identifier(pzInsKey) of the message that needs to be edited.
   * @param param.parentMessageID The unique identifier(pzInsKey) of the parent message that needs to be edited.
   * @param param.message         The new message that replaces the existing message.
   * @param param.c11nEnv         The name of the context where the API is being called.
   * @param param.isReply         The flag that determines if you want to post a message or a reply to a message.
   *                                          The default value is false. Set isReply to true if you want to edit a reply for a message.
   *                                          Set isReply to false if you want to edit a message.
   * @returns A Promise that resolves to an object.
   */
  editMessage = ({
    messageID,
    parentMessageID,
    message,
    c11nEnv,
    isReply = false
  }: {
    messageID: string;
    parentMessageID: string;
    message: string;
    c11nEnv: C11nEnv;
    isReply: boolean;
  }): Promise<object> => {
    const body = JSON.stringify({ message });
    const queryPayload = { messageID };
    return invokeRestApi(EDIT_FEED_MESSAGE, { queryPayload, body, headers }, c11nEnv.getContextName())
      .then((response: any) => {
        if (response?.status === 200 && response?.data) {
          const messageData = response?.data;
          if (isReply) {
            messageData.parentMessageID = parentMessageID;
            const { pxResults, caseMentions } = this.buildMessageObject(messageData, isReply, c11nEnv, false);
            c11nEnv.updateState({ pulse: { messages: { [parentMessageID]: { pxResults } } } });
            return caseMentions;
          }
          const { messages, caseMentions } = this.buildMessageObject(messageData, isReply, c11nEnv, false);
          c11nEnv.updateState({ pulse: { messages } });
          return caseMentions;
        }
        c11nEnv.reportError(`editMessage call failed with status ${response.status}`, response);
        return '';
      })
      .catch((error: any) => {
        c11nEnv.reportError(': Error occurred during ajax call at editMessage API : ', error.response);
      });
  };

  /**
   * @private
   * @param messageData   The object that contains the newly added/edited message details.
   * @param isReply       The flag that determines if you want to post/edit a message or a reply to a message.
   * @param c11nEnv       The name of the context where the API is being called.
   * @param hasAttachment The flag that determines if you have an attachment for a message.
   * @returns MessageObject The messageObject
   */
  buildMessageObject = (messageData: any, isReply: boolean, c11nEnv: C11nEnv, hasAttachment = false): any => {
    const messageObject = {
      ID: messageData?.ID || '',
      tags: messageData?.tags || [],
      pyHasAttachment: hasAttachment,
      pzInsKey: messageData?.ID || '',
      pyMessage: messageData?.message || '',
      mentions: messageData?.mentions || [],
      postedByUser: messageData?.postedByUser,
      pxCreateDateTime: messageData?.postedTime || '',
      pxUpdateDateTime: messageData?.updatedTime || '',
      parentMessageID: messageData?.parentMessageID || '',
      pyIsEdited:
        typeof messageData?.isEdited === 'string' ? messageData?.isEdited === 'true' : messageData?.isEdited || false,
      pyFeed: {
        pyPostedOn: messageData?.postedTime || '',
        pyFeedTitle: messageData?.postedByUser?.name || '',
        pyIconType: 'user',
        pyCommentContext: 'pzInsKey',
        pyIconReference: 'pi pi-case'
      },
      pyContext: '',
      pxIcon: 'globe',
      pyMessageViewReference: 'pzPostDetails'
    };
    const messages = c11nEnv.getValue('pulse.messages');
    if (isReply) {
      let { pxResults = [] }: { pxResults?: any[] } =
        Object.values(messages)?.find((item: any) => item?.ID === messageObject?.parentMessageID) || {};
      const index = pxResults?.findIndex((item: any) => item?.pzInsKey === messageObject?.ID);
      if (index !== -1) {
        const pxResultsCopy = [...pxResults];
        pxResultsCopy[index] = { ...pxResultsCopy[index], ...messageObject };
        pxResults = pxResultsCopy;
      } else {
        pxResults = [...pxResults, messageObject];
      }
      return {
        pxResults,
        messages: {},
        messageIDs: [],
        caseMentions: this.getCaseMentions(pxResults)
      };
    }
    const messageIDs = c11nEnv.getValue('pulse.messageIDs');
    if (messageIDs) {
      const updatedMessage = messageIDs.includes(messageObject.ID)
        ? { ...messages[messageObject.ID], ...messageObject }
        : messageObject;
      const updatedMessages = { ...messages, [messageObject.ID]: updatedMessage };
      const updatedMessageIDs = messageIDs.includes(messageObject.ID) ? messageIDs : [messageObject.ID, ...messageIDs];
      return {
        messages: updatedMessages,
        messageIDs: updatedMessageIDs,
        caseMentions: this.getCaseMentions(Object.values(updatedMessages))
      };
    }
  };

  /**
   * @private
   * @param array This param carries the list of mention objects
   * @returns The list of mention objects excluding the user mentions and duplicates
   */
  getCaseMentions = (array: any[]) => {
    const mentionsData = array
      ?.filter((item: any) => item?.mentions && Array.isArray(item?.mentions) && item.mentions?.length > 0)
      ?.map((item: any) => item.mentions)
      ?.concat(this.mentions);
    return (
      [...new Map(mentionsData.flat()?.map((item: any) => [item.mentionsID, item])).values()]
        ?.filter((item) => Object.prototype.hasOwnProperty.call(item, 'mentionsType'))
        ?.filter((item: any) => !item?.mentionsType?.toLowerCase().includes('user')) || []
    );
  };

  /**
   * Like or unlike a message.
   * @function
   * @static
   * @example <caption>In this example, the API likes the message whose messageID is PEGASOCIAL M-100.</caption>
   *  const payload = {
   *    pulseContext:'PEGAPROJMGMT-WORK TASK-100',
   *    likedBy: false,
   *    messageID:'PEGASOCIAL M-100',
   *    isReply: false,
   *    c11nEnv: getPConnect()
   *  }
   * PCore.getFeedUtils().likeMessage(payload)
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API dislikes the message whose messageID is PEGASOCIAL M-100.</caption>
   *  const payload = {
   *    pulseContext:'PEGAPROJMGMT-WORK TASK-100',
   *    likedBy: true,
   *    messageID:'PEGASOCIAL M-100',
   *    isReply: false,
   *    c11nEnv: getPConnect()
   *  }
   * PCore.getFeedUtils().likeMessage(payload)
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API likes the reply whose messageID is PEGASOCIAL M-100.</caption>
   *  const payload = {
   *    pulseContext:'PEGAPROJMGMT-WORK TASK-100',
   *    likedBy: false,
   *    messageID:'PEGASOCIAL M-100',
   *    isReply: true,
   *    c11nEnv: getPConnect()
   *  }
   * PCore.getFeedUtils().likeMessage(payload)
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API dislikes the reply whose messageID is PEGASOCIAL M-100.</caption>
   *  const payload = {
   *    pulseContext:'PEGAPROJMGMT-WORK TASK-100',
   *    likedBy: true,
   *    messageID:'PEGASOCIAL M-100',
   *    isReply: true,
   *    c11nEnv: getPConnect()
   *  }
   * PCore.getFeedUtils().likeMessage(payload)
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @param param               The object that contains all the required data to like or unlike a specific message.
   * @param param.pulseContext  The name of the application context or case context for which the feed must be fetched.
   * @param param.likedBy       The value that determines whether to like or unlike the message.
   * @param param.messageID     The ID(pzInsKey) of the message that needs to be liked or unlike.
   * @param param.isReply       The flag that determines whether the number of likes must be obtained for a message or a reply to a message.
   * @param param.c11nEnv       The name of the context where the API is being called.
   */
  likeMessage = ({
    pulseContext,
    likedBy: unLiked,
    messageID,
    isReply,
    c11nEnv
  }: {
    pulseContext: string;
    likedBy: boolean;
    messageID: string;
    isReply: boolean;
    c11nEnv: C11nEnv;
  }) => {
    const routeKey = unLiked ? UNLIKE_FEED_MESSAGES : LIKE_FEED_MESSAGES;
    const body = JSON.stringify({
      ContextClass: pulseContext
    });
    const queryPayload = {
      pulseContext
    };
    invokeRestApi(routeKey, { body, queryPayload, headers }, c11nEnv.getContextName())
      .then((response: any) => {
        if (response.status === 200) {
          if (isReply) {
            const msg = c11nEnv.getValue(`pulse.messages.${messageID}`);
            const pxResults =
              msg?.pxResults &&
              Array.isArray(msg?.pxResults) &&
              msg.pxResults?.length > 0 &&
              msg.pxResults?.map((reply: any) => {
                reply = { ...reply };
                /* istanbul ignore else */
                if (reply.pzInsKey === pulseContext) {
                  let updatedLikeCount = 0;
                  let updatedLikedFlag = false;
                  const likeCount = reply.pyLikes ? reply?.pyLikes?.pxLikeCount : 0;
                  if (unLiked) {
                    updatedLikeCount = likeCount - 1;
                    updatedLikedFlag = false;
                  } else {
                    updatedLikeCount = likeCount + 1;
                    updatedLikedFlag = true;
                  }
                  reply.pyLikes = {
                    pxLikeCount: updatedLikeCount,
                    pxIsLiked: updatedLikedFlag
                  };
                }
                return reply;
              });

            c11nEnv.updateState({
              pulse: {
                messages: {
                  [messageID]: {
                    pxResults
                  }
                }
              }
            });
          } else {
            const msg = { ...c11nEnv.getValue(`pulse.messages.${messageID}`) };
            const likeCount = msg.pyLikes ? msg.pyLikes.pxLikeCount : 0;
            let updatedLikeCount = 0;
            let updatedLikedFlag = false;

            if (unLiked) {
              updatedLikeCount = likeCount - 1;
              updatedLikedFlag = false;
            } else {
              updatedLikeCount = likeCount + 1;
              updatedLikedFlag = true;
            }

            msg.pyLikes = {
              pxLikeCount: updatedLikeCount,
              pxIsLiked: updatedLikedFlag
            };

            c11nEnv.updateState({
              pulse: {
                messages: {
                  [pulseContext]: msg
                }
              }
            });
          }
        } else {
          c11nEnv.reportError(`likeMessage call failed with status ${response.status}`, response);
        }
      })
      .catch((error: any) => {
        c11nEnv.reportError(': Error occurred during ajax call at likeMessage API : ', error.response.data);
      });
  };

  /**
   * Deletes a message from a given context.
   * @function
   * @static
   * @example <caption>In this example, the API removes the reply whose ID is PEGASOCIAL M-101 from a message whose ID is PEGASOCIAL M-100.</caption>
   * PCore.getFeedUtils().deleteMessage('PEGASOCIAL M-100', true, 'PEGASOCIAL M-101', getPConnect())
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API removes the message whose messageID is PEGASOCIAL M-100</caption>
   * PCore.getFeedUtils().deleteMessage('PEGASOCIAL M-100', false, '', getPConnect())
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @param messageID  The ID(pzInsKey) of the message that needs to be deleted.
   * @param isReply    The flag that determines if you want to delete a message or a reply to a message.
   * @param replyID    The ID of the reply that needs to be deleted.
   * @param c11nEnv    The name of the context where the API is being called.
   */
  deleteMessage = (messageID: string, isReply: boolean, replyID: string, c11nEnv: C11nEnv) => {
    let messageKey = messageID;
    if (isReply) {
      messageKey = replyID;
    }
    const queryPayload = {
      messageID: messageKey
    };

    invokeRestApi(DELETE_FEED_MESSAGE, { queryPayload }, c11nEnv.getContextName())
      .then((response: any) => {
        if (response.status === 200) {
          if (isReply) {
            const msg = c11nEnv.getValue(`pulse.messages.${messageID}`);
            const pxResults =
              msg?.pxResults &&
              Array.isArray(msg?.pxResults) &&
              msg.pxResults?.length > 0 &&
              msg.pxResults?.filter((reply: any) => reply.pzInsKey !== replyID);

            c11nEnv.updateState({
              pulse: {
                messages: {
                  [messageID]: {
                    pxResults
                  }
                }
              }
            });
          } else {
            const msgIDs = c11nEnv.getValue('pulse.messageIDs');
            const newMsgIDs =
              msgIDs && Array.isArray(msgIDs) && msgIDs?.length > 0 && msgIDs?.filter((msgID) => msgID !== messageID);

            const msgs = { ...c11nEnv.getValue('pulse.messages') };
            delete msgs[messageID];

            c11nEnv.updateState({
              pulse: {
                messageIDs: newMsgIDs,
                messages: msgs
              }
            });
          }
        } else {
          c11nEnv.reportError(`deleteMessage call failed with status ${response.status}`, response);
        }
      })
      .catch((error: any) => {
        c11nEnv.reportError(': Error occurred during ajax call at deleteMessage API : ', error.response);
      });
  };

  /**
   * Obtains the list of users who liked the message.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the list of users who liked the message with messageID PEGASOCIAL M-100</caption>
   * PCore.getFeedUtils().getLikedUsers(PEGASOCIAL M-100, getPConnect())
   * .then(response => {
   *    // response array
   * }).catch(err => {
   *   // errors
   * });
   *
   * @param messageID         The ID(pzInsKey) of the message for which that liked users must be fetched.
   * @param c11nEnv           The name of the context where the API is being called.
   * @returns A Promise that resolves to an object.
   */
  getLikedUsers = (messageID: string, c11nEnv: C11nEnv): Promise<object> => {
    const queryPayload = {
      messageID
    };
    return invokeRestApi(GET_FEED_MESSAGE_LIKES, { queryPayload }, c11nEnv.getContextName())
      .then((response: any) => response?.data?.users)
      .catch((error: any) => {
        c11nEnv.reportError(': Error occurred during ajax call at getLikedUsers API : ', error.response);
      });
  };

  /**
   * Obtains the list of options for the selected object that can be mentioned in a Pulse post.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the list of cases that can be mentioned in a Pulse post.</caption>
   * PCore.getFeedUtils().getMentionSuggestions({searchFor:"case", mentionsType = 'Cases', listSize:5}, getPConnect())
   * .then(mentionsResponse => {
   *    // mentionsResponse array
   * }).catch(err => {
   *   // errors
   * });
   * @param mentionProps  The object that contains the search parameters for obtaining the list of options for the selected type of object.
   * @param c11nEnv       The name of the context where the API is being called.
   * @returns A Promise that resolves to an object.
   */
  getMentionSuggestions = (mentionProps: any, c11nEnv: C11nEnv): Promise<object> => {
    const { pulseContext, searchFor, mentionsType, listSize = 5 } = mentionProps;
    const queryPayload = {
      pulseContext,
      searchFor,
      mentionsType,
      listSize
    };

    for (let i = 0; i < this.mentionsTagsCancelTokenSource.length; i += 1) {
      this.mentionsTagsCancelTokenSource[i].cancel();
    }
    const newCancelTokenSource = getCancelTokenSource();
    this.mentionsTagsCancelTokenSource.push(newCancelTokenSource);
    return invokeRestApi(
      GET_MENTION_SUGGESTIONS,
      {
        queryPayload,
        cancelTokenSource: newCancelTokenSource
      },
      c11nEnv.getContextName()
    )
      .then((response: any) => {
        this.mentionsTagsCancelTokenSource = [];
        if (response.status === 200 && response.data) return response?.data;
        c11nEnv.reportError(`Get mention suggestions call failed ${response.status}`, response.data);
        return [];
      })
      .catch((err: any) => {
        /* istanbul ignore else */
        if (!isRequestCanceled(err)) {
          c11nEnv.reportError(': Error occurred during ajax call at getMentionSuggestions API : ', err.response.data);
        }
      });
  };

  /**
   * Obtains the list of available types of objects that can be mentioned in a Pulse post.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the list of available types of objects that can be mentioned in a Pulse post.</caption>
   * PCore.getFeedUtils().getMentionTypes()
   * .then(response => {
   *    // response array
   * }).catch(err => {
   *   // errors
   * });
   * @param c11nEnv      The name of the context where the API is being called.
   * @returns A Promise that resolves to an object.
   */
  getMentionTypes = (c11nEnv: C11nEnv): Promise<object> => {
    return invokeRestApi(GET_MENTION_TYPES, {}, c11nEnv.getContextName())
      .then((response: any) => response?.data)
      .catch((err: any) => {
        c11nEnv.reportError(': Error occurred during ajax call at getMentionTypes API : ', err.response.data);
      });
  };

  /**
   * Obtains the options suggested for selecting a tag for a given context.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the options suggested for selecting a tag for the given context.</caption>
   * PCore.getFeedUtils().getTagSuggestions({searchFor:"test", listSize:5}, getPConnect())
   * .then(tagsResponse => {
   *    // tagsResponse array
   * }).catch(err => {
   *   // errors
   * });
   * @param tagProps      The object that contains the search parameters for obtaining the list of tags.
   * @param c11nEnv       The name of the context where the API is being called.
   * @returns A Promise that resolves to an object.
   */
  getTagSuggestions = (tagProps: any, c11nEnv: C11nEnv): Promise<object> => {
    const { searchFor, listSize = 5 } = tagProps;
    const queryPayload = {
      searchFor,
      listSize
    };

    for (let i = 0; i < this.mentionsTagsCancelTokenSource.length; i += 1) {
      this.mentionsTagsCancelTokenSource[i].cancel();
    }
    const newCancelTokenSource = getCancelTokenSource();
    this.mentionsTagsCancelTokenSource.push(newCancelTokenSource);

    return invokeRestApi(
      GET_TAG_SUGGESTIONS,
      {
        queryPayload,
        cancelTokenSource: newCancelTokenSource
      },
      c11nEnv.getContextName()
    )
      .then((response: any) => {
        this.mentionsTagsCancelTokenSource = [];
        let tagSuggestions = [];
        if (response.status === 200 && response?.data) {
          tagSuggestions =
            response?.data?.tags &&
            Array.isArray(response?.data?.tags) &&
            response.data.tags.length > 0 &&
            response.data.tags?.map((tag: any) => `#${tag}`);
        } else {
          c11nEnv.reportError(`Get tag suggestions call failed ${response.status}`, response.data);
        }
        return tagSuggestions;
      })
      .catch((err: any) => {
        /* istanbul ignore else */
        if (!isRequestCanceled(err)) {
          c11nEnv.reportError(': Error occurred during ajax call at getTagSuggestions API : ', err.response.data);
        }
      });
  };
}

export default new FeedUtils();
