import Utils from '../../../utils/utils';

class ReplacePage {
  static execute(contextName: string, target: string, payload: object, pageReference: string) {
    const actionPayload = {
      instruction: 'REPLACE',
      content: {
        ...Utils.unflatten(payload)
      }
    };
    return {
      content: { ...actionPayload.content },
      target: Utils.buildInstructionPageReference(pageReference, target),
      instruction: 'REPLACE'
    };
  }
}

export default ReplacePage;
